<template>
	<div >
		<div class="tradingRecord qrcode">
			<div style="height:1px"></div>
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p>你的好友给您借款100元,请扫码进行消费</p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardPay" width="50%">
				</div>
				<p>扫一扫,向商家付款</p>
			</div>
		</div>

	</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	urlCardPay:'',
		    	dataKey:''
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				_this.busId= _this.$route.params.busId;
				_this.dataKey= _this.$route.params.dataKey;
				console.log(_this.dataKey)
				_this.url=window.location.href;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				// _this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.authorizeOrLogin_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	if(data.code==0){
	                		_this.urlCardPay=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.memberLentImage_GET+"?busId="+_this.busId+"&memberLentKey="+_this.dataKey;
	                		console.log('shareOfferpay',_this.urlCardPay)
	                	}
	                	
	                }
				})
			}
		},
		created:function(){
	    	var _this=this;
	    	_this.loadData();
	        
	    }
	}
</script>
<style scope>

</style>


