<template>
		<div class='recommendWithDraw'>
			<div class="recommend recommendHeight">
				<div class="recommendTitle clearfix">
					<div class="clearfix recommendTitleHead topuphead">
						<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
						<a class="active-bar" :style="styleObject"></a>
						<div class="fl" :class='{isActive:1===isShow}' @click="clickMenu(1)">推荐明细</div>
						<div class="fl" :class='{isActive:2===isShow}' @click="clickMenu(2)">提现</div>
					</div>
					<!-- 推荐明细表 -->
					<div class="recommendDetail" v-if="isRecommendDetail">
						<p>余额(元)</p>
						<p class="total">{{tuijianMoneyLf}}.<span>{{tuijianMoneyRg}}</span></p>
						<div class="clubTotal">
							<p>会员卡成功推荐<span>{{memberCount}}</span>人,累计收益<span  @click="unfold(1)"><i :class='isunfold==1?"icon-shang-copy":"icon-you"' class="icon iconfont " ></i></span></p>
							<!-- <p>会员卡成功推荐<span>50</span>人,累计收益<span  :class='isunfold==1?"bottom":"left"' @click="unfold(1)"></span></p> -->
							<ul v-show="isunfold==1">
								<li>现金：<span>{{memberMoneyCount}}元</span></li>
								<li>粉币：<span>{{fenbiMemberCount}}粉币</span></li>
								<li>流量：<span>{{flowMemberCount}}M</span></li>
								<li>积分：<span>{{jifenMemberCount}}积分</span></li>
							</ul>
						</div>
						<div class="couponTotal">
							<p>优惠券成功领取<span>{{getCount}}</span>人,使用<span>{{useCount}}</span>人,累计收益<span  @click="unfold(2)"><i :class='isunfold==2?"icon-shang-copy":"icon-you"' class="icon iconfont " ></i></span></p>
							<ul v-show="isunfold==2">
								<li>现金：<span>{{userMoney}}元</span></li>
								<li>粉币：<span>{{fenbiYhjCount}}粉币</span></li>
								<li>流量：<span>{{flowYhjCount}}M</span></li>
								<li>积分：<span>{{jifenYhjCount}}积分</span></li>
							</ul>
						</div>
					</div>
					<!-- 提现明细表 -->
					<div class="withDraw" v-if="isWithDraw">
						<p>可提现金额(元)</p>
						<p class="total">{{tuijianMoneyLf}}.<span>{{tuijianMoneyRg}}</span></p>
						<p>累计提现：<span>{{pickMoney}}</span></p>
					</div>
				</div>
				<div class="recommendCom" :class="{hasWithDraw:isWithDraw}">
					<!-- 推荐明细内容 -->
					<div v-if="isRecommendDetail">	
						<div class="hasRecommend hasRecommendDetail" v-if="hasData" >
							<div class="IntegralDataCom clearfix" v-for="(item,index) in recommendData">
								<div class="IntegralDataComLe" >
									<p class="IntegralDataComTop">
										{{item.name}}({{item.recommendType==0?'会员卡':'优惠券'}})
										<a href="" class='fr'>{{item.datetime}}</a>
									</p>
								</div>
								<div class=" IntegralDataComRi IntegralDataComBot">
									<span>+¥{{item.money}}</span><span>+{{item.integral}}积分</span><span>+{{item.flow}}M</span><span>+{{item.fenbi}}粉币</span>
								</div>
							</div>
						</div>
						<NoMessage v-else messageCom="暂无推荐记录"></NoMessage>
						<ClubFoot></ClubFoot>
						<div class="RecommendDetailHeight " v-if="hasData"></div>
					</div>
					<!-- 提现明细内容 -->
					<div v-if="isWithDraw">	
						<div class="hasRecommend hasRecommendWithDraw" v-if="hasWithDrawData" >
							<div class="IntegralDataCom clearfix" v-if="(item,index) in pickListData">
								<div class="IntegralDataComLe w60 fl">
									<p >提现</p>
									<p><a href="">2017-10-12 14:52:12</a></p>
								</div>
								<div class=" IntegralDataComRi fr w30 ">
									<span>¥100.00</span>
								</div>
							</div>
						</div>
						<NoMessage v-else messageCom="暂无提现记录" :class="{WithDrawNoData:isWithDraw}"></NoMessage>
						<ClubFoot></ClubFoot>
						<div class="WithDrawHeight " v-if="hasData"></div>
					</div>
				</div>
			</div>
			
			<!-- <div style="height:1.8rem;"></div> -->
			
			
			<!-- <div class="WithDrawHeight " v-if="hasPickListData"></div> -->
			<!-- <div class="buttonHeight " v-if="hasData"></div> -->
			<div class="integralButton" v-if="isRecommendDetail">
				<router-link tag="p" :to="{ name: 'qrcode',params: { busId: busId }}" >
					<button class="active" type="button" name="" value="" >我要推荐</button>
				</router-link>
			</div>
			<div class="integralButton" v-if="isWithDrawBUt">
				<router-link tag="p" :to="{ name: 'withdraw',params: { busId: busId,totalMoney:totalMoney }}">
					<button class="tixian buttonDisable active" type="button" name="" value="" >我要提现</button>
					<p>可提现金额≥{{lessPickMoney}}元，才可以提现</p>
				</router-link>
			</div>
			<!-- 商家余额不足 -->
			<div class="notEnough" v-if="isnotEnough">
				<div class="notEnoughTips">
					<p>提示</p>
					<p>商家余额不足，继续提现请联系商家</p>
					<p>确定</p>
				</div>
			</div>
		</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import NoMessage from "@/components/NoMessage";
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	isWithDraw:false,
		    	isShow:0,
		    	isunfold:false,
		    	isnotEnough:false,
		    	isRecommendDetail:true,
		    	isWithDrawBUt:false,
		    	hasData:true,
		    	busId:0,
		    	url:'',
		    	hasWithDrawData:false,
		    	styleObject: {
				    width: '102%',
				    transform: 'translateX(0px)'
				},
				recommendData:[],//推荐
				getCount:0,
				useCount:0,
				jifenMemberCount:0,
				fenbiMemberCount:0,//  会员粉币
				flowMemberCount:0,// 会员流量
				memberMoneyCount:0,// 会员金额
				jifenYhjCount:0,//  优惠券积分
				fenbiYhjCount:0,//  优惠券粉币
				flowYhjCount:0,//   优惠券流量
				userMoney:0,//  优惠券金额
				memberCount:0,
				tuijianMoney:0,// 提取总金额
				tuijianMoneyLf:0,
				tuijianMoneyRg:0,
				totalMoney:0,
				hasPickListData:false,

				pickListData:[],//提取
				lessPickMoney:0,
				pickMoney:0,
				payType:1
			}
		},
		components:{
		    ClubFoot,
		    NoMessage
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findRecommend_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('recommendData',data);
	                	if(data.code==0){
	                		_this.recommendData=data.data.recommends;
	                		_this.getCount =data.data.getCount;
	                		_this.useCount =data.data.useCount ;
	                		_this.jifenMemberCount=data.data.jifenMemberCount;
	                		_this.flowMemberCount=data.data.flowMemberCount;
	                		_this.fenbiMemberCount  =data.data.fenbiMemberCount  ;
	                		_this.memberMoneyCount=data.data.memberMoneyCount;
	                		_this.jifenYhjCount=data.data.jifenYhjCount;
	                		_this.fenbiYhjCount=data.data.fenbiYhjCount;
	                		_this.flowYhjCount=data.data.flowYhjCount;
	                		_this.userMoney=data.data.userMoney;
	                		_this.lessPickMoney=data.data.lessPickMoney;
	                		_this.memberCount=data.data.memberCount;
	                		_this.totalMoney=data.data.tuijianMoney;
	                		_this.tuijianMoney=_this.totalMoney.toString().split('.');
	                		if(_this.tuijianMoney.length==1){
	                			_this.tuijianMoneyLf=Number(_this.tuijianMoney[0]);
	                			_this.tuijianMoneyRg='00';
	                		}
	                		if(_this.tuijianMoney.length==2){
	                			_this.tuijianMoneyLf=Number(_this.tuijianMoney[0]);
	                			_this.tuijianMoneyRg=Number(_this.tuijianMoney[1]);
	                		}

	                		// _this.payType=data.data.payType;
	                		if(_this.recommendData.length!=0||_this.recommendData.length!=undefined){
	                			for(let i=0;i<_this.recommendData.length;i++){
	                				_this.recommendData[i].datetime=Lib.M.formatTime(_this.recommendData[i].datetime);
	                			}
	                			_this.hasData=true;
	                		}else{
	                			_this.hasData=false;
	                		}
	                		_this.pickListData=data.data.pickList;
	                		if(_this.pickListData.length!=0||_this.pickListData.length!=undefined){
	                			for(let i=0;i<_this.pickListData.length;i++){
	                				_this.pickListData[i].datetime=Lib.M.formatTime(_this.pickListData[i].datetime);
	                			}
	                			_this.hasData=true;
	                		}else{
	                			_this.hasData=false;
	                		}
	                		
	                		
	                	}
	                }
				})
			},
			clickMenu(index){
				var _this=this;
				_this.isShow=index;
				let ind;
				if(index==1){
					ind=0;
					_this.isRecommendDetail=true;
					_this.isWithDraw=false;
					_this.isWithDrawBUt=false;
					_this.hasWithDrawData=false;
				}else{
					ind=index;
					if(_this.payType==0){
						_this.isWithDrawBUt=false;
					}
					if(_this.payType==1){
						_this.isWithDrawBUt=true;
					}
					_this.isRecommendDetail=false;
					_this.isWithDraw=true;
					_this.hasWithDrawData=false;
				}
				_this.styleObject.transform='translateX('+parseInt(ind)*parseInt(_this.styleObject.width)+'%)';
				
			},
			unfold(cur){
				if(this.isunfold==cur){
					this.isunfold=false
				}else{
					this.isunfold=cur;
				}
			}
		},
		mounted:function(){
            this.loadData();

        }

	}
</script>
<style>


</style>


