<template>
	<div class="club">
		<!-- 会员卡图片 -->
		<div class="clubCard clearfix" :style="{background:cmUrl}" @click="clubCardPay">
			<div class="head" :class="{goRight:isleft}">
				<img class="headImg fl" :class="{noImg:gtLoginUrl}" :src="gtLoginUrl" v-show="isGtLoginUrl">
				<span class="fl" :style="gtNameObject">{{cardName}}</span>
			</div>
			<div class="grade clearfix">
				<div class="gradeCom">
					<p :style="gtNameObject">{{cardData.gt_grade_name}}</p>
					<p :style="gtCardNoObject">{{cardData.cardNo}}</p>
				</div>
				<div class="weix">
					<svg class="icon" aria-hidden="true">
	                    <use xlink:href="#icon-erweima"></use>
	                </svg>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
export default{
	data() {
	    return {
		    headImg:'',
		    grade:1,
		    isleft:0,
		    memberData:[],
		    cardData:{
		    	'gt_grade_name':'会员等级1',
		    	'cardNo':'00000000001'
		    },
		    gtLoginUrl:'',
		    isGtLoginUrl:false,
		    cardName:'会员卡',
		    cmUrl:'',
		    busId:0,
		    url:'',
		    gtNameObject: {
			    color: '#333'
			},
			gtCardNoObject:{
				color: '#333'
			}
		}
	},
	methods:{
		loadData:function(){
			var _this=this;
			_this.busId= _this.$route.params.busId;
			_this.url=window.location.href;
			var json={};
			json["busId"]=_this.busId;
			json["requestUrl"]=_this.url;
			Lib.M.ajax({
				'url':memberAPI.activeAPI.findMember_GET,
                'type': 'GET',
                'data':{
                   "json":json
                },
                'success':function (data){
                	_this.memberData=data.data.member;
                	_this.cardData=data.data.card;
                	_this.gtNameObject.color=_this.cardData.gt_name_color;
                	_this.gtCardNoObject.color=_this.cardData.gt_text_color;
                	_this.isleft=_this.cardData.isleft;
                	if(_this.cardData.cm_url==null){
                		// _this.cmUrl=data.data.path;
                	}else{	
                		_this.cmUrl=data.data.path+_this.cardData.cm_url;
                	}
                	if(_this.cardData.gt_login_url==undefined){
                		_this.isGtLoginUrl=false;
                	}else{	
                		_this.isGtLoginUrl=true;
                		_this.gtLoginUrl=data.data.path+_this.cardData.gt_login_url;
                	}
                	_this.cmUrl='url('+_this.cmUrl+') no-repeat 100%/100%';
                	console.log('headdata',data);
                }
			})
		},
		clubCardPay(){
			var _this=this;
			_this.$router.push({ name: 'offerpay', params: _this.busId})
		}
	},
    created:function(){
        this.loadData();

    }
	
}
</script>
<style scoped>
	.noImg{
		padding-left: 0.25rem;

	}
</style>


