<template>
		<div class="Topup Integral">
			<div class="Topuptitle">
				<div class="clearfix topuphead">
					<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
					<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
						<a >首页</a>
					</router-link>
				</div>
				<p>当前粉币</p>
				<p class="residue">{{fenbi}}<span>粉币</span></p>
			</div>
			<div class="Topupoptions">
				<div class="options clearfix">
					<!-- 有数据 -->
					<div class="IntegralData" v-if="hasData">
						<ul
						  v-infinite-scroll="loadMore"
						  infinite-scroll-disabled="loading"
						  infinite-scroll-distance="10">
						  <li class="IntegralDataCom flowRecord clearfix"  v-for="(item,index) in fbData">
						  		<div class="IntegralDataComLe w85 fl">
									<p >谷通商城(粉币商城)</p>
									<p><a href="">地址:广东省惠州市惠城区东平大道20号10楼1004室</a></p>
								</div>
								<div class=" IntegralDataComRi fr w10 ">
									<span><i class="icon iconfont icon-you"></i></span>
								</div>
						  </li>
						</ul>
					</div>
					<!-- 没有数据 -->
					<NoMessage v-else messageCom="暂无粉币记录"></NoMessage>
				</div>
				<ClubFoot></ClubFoot>
				<div class="height4"></div>
			</div>
		</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import NoMessage from "@/components/NoMessage";
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	hasData:false,
		    	loading:false,
		    	fenbi:0,
		    	busId:0,
		    	url:'',
		    	page:1,
		    	fbData:[],
		    	pageData:[]
			}
		},
		components:{
		    ClubFoot,
		    NoMessage
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['page']=_this.page;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findCardrecordNewFenbi_GET,
	                'type': 'GET',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('fenbi',data);
	                	if(data.code==0&&data.data.page.length>0){
	                		_this.fbData=data.data.page;
	                		_this.hasData=true;
	                		_this.pageData=_this.fbData;
	                	}
	                	_this.jifen=data.data.jifen;
	                }
				})
			},
			loadMore() {
				var _this=this;
				_this.loading = true;
				if(_this.pageData!=0){
			  		_this.page+=1;
			  	}else{
			  		return false;
			  	}
			  	var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['page']=_this.page;
				setTimeout(() => {
			  		Lib.M.ajax({
						'url':memberAPI.activeAPI.findCardrecordNewFenbi_GET,
		                'type': 'GET',
		                'data':{
		                   "json":json
		                },
		                'success':function (data){
		                	console.log(data.data.page.length);
		                	_this.pageData=[];
		                	if(data.data.page.length!=0){
		                		if(data.code==0&&data.data.page.length>0){
			                		_this.pageData=data.data.page;
			                		for(let i=0;i<_this.pageData.length;i++){
			                			_this.fbData.push(_this.pageData[i]);
			                		}
			                	}
		                	}else{
		                	}
		                	_this.loading = false;
		                }
					})
				}, 1000);
			}
		},
		created:function(){
            this.loadData();

        }

	}
</script>
<style scope>
	.flowRecord .IntegralDataComLe p:last-child{
		overflow: hidden; 
		text-overflow:ellipsis; 
		white-space: nowrap;
	}
	.Topup .Topuptitle a{
		color: #fff;
		font-size: 
	}
	.Integral .Topupoptions .options .IntegralDataComRi .icon{
		font-size: 0.4rem;
		color: #9b9b9b;
	}
</style>


