<template>
	<div class="pickclub">
		<div class="club pickclubDiv">
			<!-- 会员卡图片 -->
			<img src="static/images/1.jpg" class="pickclubImg">
		</div>
		<div class="clubType" >
			<div class="cardType clearfix " v-if="ifFirst">
				<p>选择会员卡类型</p>
				<span v-for="(item, index) in navData" :class="{active:index===isChoose}" @click="clickNav(index)">{{item.ctName}}</span>
			</div>
			<!-- 会员首次基本资料填写 -->
			<div  v-if="ifCardList">
				<div :class="{carAni:ifCardList}">
					<div class="cardType ">
						<!-- <h3>已选择{{buyMoney}}元卡</h3> -->
						<h3>填写会员卡资料</h3>
						<div class="phone">
							<label for="phone">手机号</label>
							<input type="number" name="" v-model='phone' @blur="verification()"  value="" :placeholder='memberoption.phoneOption==1?"必填":"选填"' id="phone" >
							<a href="javascript:;" v-if="isGetCode" class="phonemessage">{{timeleft}}秒后可重发</a>
							<a href="javascript:;" v-else @click="getCode">获取验证码</a>
						</div>
						<div class="phone authCode">
							<label for="authCode">验证码</label>
							<input type="text" name="" v-model="code" value="" :placeholder='memberoption.phoneOption==1?"必填":"选填"' id="authCode" @blur="checkAuthCode">
						</div>
						<div class="phone authCode">
							<label for="InvitationCode">推荐码</label>
							<input type="text" v-model="tuijianCode" name="" value="" placeholder="请输入邀请码" id="InvitationCode">
						</div>
					</div>
					<div class="cardType " v-if="cardList">
						<div class="phone username"  v-if='memberoption.nameShow1==1'>
							<label for="username">姓名</label>
							<input type="text" v-model='name' @blur="clickName"  value="" :placeholder='memberoption.nameOption==1?"必填":"选填"' id="username">
						</div>
						<div class="authCode phone sex"  v-if='memberoption.sexShow1==1'>
							<label for="sex" class="fl">性别</label>
							<input type="button" name="" :class="{sexColor:isColor}" v-model='sex' @click="clickSex" :placeholder='memberoption.sexOption==1?"必填":"选填"'  id="sex" >
							<span @click="clickSex"><i class="icon iconfont icon-you"></i></span>
						</div>
						<div class="phone username"  v-if='memberoption.birthShow1==1'>
							<label for="dateBirth">出生日期</label>
							<input type="text" name="" v-model="birth" value="" onfocus="this.blur()" readonly="readonly" @click="clickBirth" :placeholder='memberoption.birthOption==1?"必填":"选填"' id="dateBirth">
						</div>
						<div class="phone authCode"  v-if='memberoption.addrShow1==1'>
							<label for="address">地址</label>
							<input type="text" style="width:4.5rem" v-model="addr" onfocus="this.blur()" @click="addProvince" readonly="readonly" name="" value="" :placeholder='memberoption.addrOption==1?"必填":"选填"' id="address"  >
						</div>
						<div class="phone authCode"  v-if='memberoption.addrDetailShow1==1'>
							<label for="addressdetails">地址详情</label>
							<input type="text" style="width:4.5rem" name="" v-model="addressdetail" @blur='addressdetails' value="":placeholder='memberoption.addrDetailOption==1?"必填":"选填"' id="addressdetails">
						</div>
						<div class="phone authCode"  v-if='memberoption.getMoneyShow1==1'>
							<label for="annualIncome" >年收入</label>
							<input type="text" class="city" name="" v-model.number="getMoney" @blur="checkMoney" value="" :placeholder='memberoption.getMoneyOption==1?"必填":"选填"' id="annualIncome"><i>元</i>
						</div>
						<div class="phone uthCode"  v-if='memberoption.mailShow1==1'>
							<label for="email">邮箱</label>
							<input type="text" name="" v-model="email" @blur='checkEmail' value="" :placeholder='memberoption.mailOption==1?"必填":"选填"' id="email">
						</div>
						<div class="phone authCode"  v-if='memberoption.cardShow1==1'>
							<label for=" identitycard">身份证</label>
							<input type="text" name="" v-model="sfCard" @blur="checkidCard" value="" :placeholder='memberoption.cardOption==1?"必填":"选填"' id="identitycard">
						</div>
					</div>
					<div class="withdrawButton">
						<button :class="[isPickClub?'active':'buttonDisable']" @click="submitData" type="button" name="" value="" >立即领取</button>
					</div>
				</div>
			</div>
			<!-- 会员卡购买分类 -->
			<div class="purchasingCard carAni" v-else>
				
				<div class="IntegralDataCom clearfix" v-for="(item, index) in cardData" :key="index">
					<div class="IntegralDataComLe w60 fl">
						<p >{{item.gt_grade_name}}</p>
						<p><a href="">{{carMsg1}}<span>{{item.balance}}</span>{{carMsg2}}</a></p>
					</div>
					<div class=" IntegralDataComRi fr w30 ">
						<a href="javascript:;" @click="buyCard(index)">购买<span>¥{{item.buyMoney}}</span></a>
					</div>
				</div>
			</div>
		</div>
		<!-- 性别选择弹出框 -->
		<div class="overlayer" @touchmove.prevent v-show="isCheckPopUp" @click="closeOverlayer">
		<transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
			<div class="fileUpload" v-if="isSexSelect">
				<div class="fileUploadBg">
					<p><input type="radio" v-model='man' @click="sureCancelman" value="男" id="Boy"> 男</p>
					<p><input type="radio"  v-model='gril' @click="sureCancelgril" name="" value="女" id="Girl">女</p>
					<p><input type="radio"  v-model='privary' @click="keepSecret" name="" value="保密" id="Girl">保密</p>
					<p><input type="radio"   @click="fileCancel" name="" value="取消" id="secret">取消</p>
				</div>
			</div>
		</transition>
		<mt-datetime-picker
		    v-model="pickerVisible"
			type="date"
			ref="picker"
			:endDate="endDate"
			:startDate="startDate"
			year-format="{value} 年"
			month-format="{value} 月"
			date-format="{value} 日"
			@confirm="handleConfirm"
			@cancel="checkinCancel" >
		</mt-datetime-picker>
		<!-- 地址联动 -->
		<div class="address" v-show="addressShow">  
            <div class="picker-toolbar">  
                <span class="mint-datetime-action mint-datetime-cancel" @click="cancleaddress">取消</span>  
                <span class="mint-datetime-action mint-datetime-confirm" @click="selectaddress">确定</span>  
            </div>  
            <mt-picker :slots="slots" :itemHeight="40"  value-key="city_name" @change="onValuesChange" :visibleItemCount="5"></mt-picker>  
        </div> 
        </div>
		<ClubFoot></ClubFoot>
	</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import ClubCard from "@/pages/Member/ClubCard";
	import Lib from '@/assets/js/Lib';
	import { Toast } from 'mint-ui';
	import axios from 'axios'
	export default{
		data() {
		    return {
		    	isSexSelect:false,
		    	isChoose:0,
		    	ifFirst:true,
		    	ifCardList:false,
		    	userDetails:false,
		    	isTrue:false,
		    	isPickClub:false,
		    	addressShow:false,
		    	timeleft:120,
		    	cardList:false,
		    	isCheckPopUp:false,
		    	startDate:new Date(new Date().getFullYear() - 50, 0, 1),
		    	endDate:new Date(),
		    	tipMsg:{
			    	isphoneMsg:'请输入手机号码',
			    	isphoneCodeMsg:'请输入手机验证码',
			    	isnameMsg:'请输入姓名',
			    	issexMsg:'请选择性别',
			    	isbirthMsg:'请选择出生日期',
			    	isaddressdetailMsg:'请输入详细地址信息',
			    	isaddrMsg:'请填写地址信息',
			    	isgetMoneyMsg:'请输入年收入',
			    	isemailMsg:'请输入邮箱',
			    	issfCardMsg:'请输入身份证号码'
		    	},
		    	isGetCode:false,
		    	isColor:false,
		    	phone:'',
		    	name:'',
		    	man:'男',
		    	gril:'女',
		    	sex:'选填',
		    	secret:'请选择',
		    	birth:'',
		    	privary:'保密',
		    	pickerVisible:new Date(new Date().getFullYear() - 3, 5, 21),
		    	addressdetail:'',
		    	addressCt:'',
		    	getMoney:'',
		    	email:'',
		    	sfCard:'',
		    	value1:'',
		    	value2:'',
		    	value3:'',
		    	value1Id:0,
		    	value2Id:0,
		    	value3Id:0,
		    	addr:'',
		    	url:'',
		    	busId:0,
		    	ctId:0,
		    	gtId:0,
		    	code:'',
		    	buyMoney:0,
		    	payType:0,
		    	tuijianCode:'',
		    	province:[],
		    	city:[],
		    	district:[],

		    	cardData:[],
		    	carMsg1:'',
		    	carMsg2:'',

		    	memberoption:[],
		    	member:[],
		    	navData:[],
		    	checkRequired:[],
		    	checkOption:{},
		    	slots: [
			        {
			          flex: 1,
			          values: [],
			          className: 'slot1',
			          textAlign: 'left',
			          defaultIndex:3
			        },
			        {
			          flex: 1,
			          values: [],
			          className: 'slot2',
			          textAlign: 'center',
			          defaultIndex:3
			        },
			        {
			          flex: 1,
			          values: [],
			          className: 'slot3',
			          textAlign: 'right',
			          defaultIndex:3
			        }
			    ]
			}
		},

		components:{
		    ClubFoot
		},
		methods:{
			loadData(){
				var _this=this;
				
				_this.busId= _this.$route.params.busId;
				_this.tuijianCode= _this.$route.params.tuijianCode;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				_this.getData(json);
			},
			getData(data){
				var _this=this;
				var json=data;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findLingquData_GET,
                    'type': 'GET',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	console.log('first',data);
                    	_this.navData=data.data.gradeTypes;
                    	_this.payType=data.data.payTypes[0].payType;

                    	if(_this.navData!=undefined||_this.navData.length>0){
                    		if(_this.navData[0].applyType===3){
                    			_this.ifCardList=false;
	                    		_this.ctId=_this.navData[0].ctId;
	                    		json['ctId']=_this.ctId;
	                    		_this.getApplyTypeData(json,_this.ctId);
	                    	}
	                    	if(_this.navData[0].applyType!=3){
								_this.ifCardList=true;
								_this.ctId=_this.navData[0].ctId;
							}
                    	}
                    	_this.memberoption=data.data.memberoption;
                    	_this.province=data.data.basisCitys;
                    	_this.member=data.data.member;
                    	_this.phone=_this.member.phone;
                    	if(_this.memberoption.nameShow1==0&&_this.memberoption.sexShow1==0&&_this.memberoption.birthShow1==0&&_this.memberoption.addrShow1==0&&_this.memberoption.addrDetailShow1==0&&_this.memberoption.getMoneyShow1==0&&_this.memberoption.mailShow1==0&&_this.memberoption.cardShow1==0){
                    		_this.cardList=false;
                    	}else{
                    		_this.cardList=true;
                    	}
                    	//必填项写入对象
                    	_this.checkOption['code']=false;
                    	if(_this.memberoption.phoneOption==1){
                    		_this.checkOption['phoneOption']=false;
                    	}
                    	if(_this.memberoption.nameShow1==1){
	                    	if(_this.memberoption.nameOption==1){
	                    		_this.checkOption['nameOption']=false;
	                    	}
                    	}
                    	if(_this.memberoption.sexShow1==1){
	                    	if(_this.memberoption.sexOption==1){
	                    		_this.checkOption['sexOption']=false;
	                    	}
                    	}
                    	if(_this.memberoption.birthShow1==1){
	                    	if(_this.memberoption.birthOption==1){
	                    		_this.checkOption['birthOption']=false;
	                    	}
                    	}
                    	if(_this.memberoption.addrShow1==1){
	                    	if(_this.memberoption.addrOption==1){
	                    		_this.checkOption['addrOption']=false;
	                    	}
                    	}
                    	if(_this.memberoption.addrDetailShow1==1){
	                    	if(_this.memberoption.addrDetailOption==1){
	                    		_this.checkOption['addrDetailOption']=false;
	                    	}
                    	}
                    	if(_this.memberoption.getMoneyShow1==1){
	                    	if(_this.memberoption.getMoneyOption==1){
	                    		_this.checkOption['getMoneyOption']=false;
	                    	}
                    	}
                    	if(_this.memberoption.mailShow1==1){
	                    	if(_this.memberoption.mailOption==1){
	                    		_this.checkOption['mailOption']=false;
	                    	}
                    	}
                    	if(_this.memberoption.cardShow1==1){
	                    	if(_this.memberoption.cardOption==1){
	                    		_this.checkOption['cardOption']=false;
	                    	}
                    	}
                    	_this.checkPickClub();
                    	console.log('_this.checkOption',_this.checkOption)
                    },
                    'error':function(err){
                    	console.log(err);
                    }
				})
			},
			//导航栏点击
			clickNav(pos){
				var _this=this;
				var json={};
				_this.isChoose=pos;
				if(_this.navData[pos].applyType===3){
					_this.ifCardList=false;
					_this.ctId=_this.navData[pos].ctId;
					json['ctId']=_this.ctId;
					json['busId']=_this.busId;
					json['requestUrl']=_this.url;
					_this.getApplyTypeData(json,_this.ctId);
					return;
				}
				if(_this.navData[pos].applyType!=3){
					_this.ctId=_this.navData[pos].ctId;
					_this.ifCardList=true;
				}
			},
			getApplyTypeData(data,ctId){
				var _this=this;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findBuyGradeTypes_GET,
                    'type': 'GET',
                    'data':{
                       "json":data
                    },
                    'success':function (data){
                    	if(ctId==1){
                    		_this.cardData=data.data;
                    		console.log('mendata',_this.cardData);
                    		_this.carMsg1='可享销售送';
                    		_this.carMsg2="积分";
                    		return;
                    	}
                    	if(ctId==2){
                    		_this.cardData=data.data;
                    		_this.carMsg1='可享受';
                    		_this.carMsg2="折优惠";
                    		return;
                    	}
                    	if(ctId==3){
                    		_this.cardData=data.data;
                    		_this.carMsg1='赠送卡内余额';
                    		_this.carMsg2="元，并享受相应的充值优惠";
                    		return;
                    	}
                    	if(ctId==4){
                    		_this.cardData=data.data;
                    		_this.carMsg1='有效期为';
                    		_this.carMsg2="个月，并享受相应的充值优惠";
                    		return;
                    	}
                    	if(ctId==5){
                    		_this.cardData=data.data;
                    		_this.carMsg1='赠送有效次数';
                    		_this.carMsg2="次，并享受相应的充值优惠";
                    		return;
                    	}
                    	
                    }
				})
			},
			//点击购买
			buyCard(ind){
				var _this=this;
				var json={};
				_this.gtId=_this.cardData[ind].gt_id;
				console.log('buyCard',_this.cardData[ind]);
				json['busId']=_this.busId;
				json['ctId']=_this.ctId;
				json['gtId']=_this.gtId;
				json['requestUrl']=_this.url;
				json['payType']=_this.payType;
				console.log(json);
				Lib.M.ajax({
					'url':memberAPI.activeAPI.linquMemberCard_GET,
                    'type': 'GET',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	console.log('buyCard',data)
                    },
                    'error':function(data){
                    	if(data.code==5011){
                    		Toast({
								message: data.msg,
								duration:1000
							});
						}
						Toast({
							message: data.msg,
							duration:1000
						});
                    	console.log('msg',data)
                    }
				})

				// _this.ifCardList=true;
				// _this.ifFirst=false;
				// _this.buyMoney=_this.cardData[ind].buyMoney;
				// console.log(ind)
			},
			//获取省
			addProvince(){
				var _this=this;
				_this.isCheckPopUp=true;
				_this.addressShow=true;
				_this.slots[0].values=_this.province;
			},
			//获取市
			getCityData(data){
				var _this=this;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findCityByCityCode_POST,
                    'type': 'POST',
                    'data':{
                       "json":data
                    },
                    'success':function (data){
                    	_this.city=data.data;
                    	console.log("city",_this.city);
                    	if(_this.city!=undefined||_this.city.length>0){
	                		var json={};
	                		json['cityCode']=_this.value2Id;
							json['requestUrl']=_this.url;
							json['busId']=_this.busId;
							_this.getDistrictData(json);
	                	}
                    }

				})
				
			},
			//获取区
			getDistrictData(data){
				var _this=this;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findCityByCityCode_POST,
                    'type': 'POST',
                    'data':{
                       "json":data
                    },
                    'success':function (data){
                    	_this.district=data.data;
                    	console.log("district",_this.district)
                    	if(_this.district!=undefined||_this.district.length>0){
	                		_this.slots[1].values=_this.city;
							_this.slots[2].values=_this.district;
	                	}
                    }

				})
			},
			onValuesChange(picker, values) {
				var _this=this;
				console.log(values);
				if(values[0]!=undefined){
					var provinceVal=values;
					_this.value1=provinceVal[0].city_name;
					_this.value1Id=provinceVal[0].city_code;
					if(values[1]!=undefined){
						_this.value2=provinceVal[1].city_name;
						_this.value2Id=provinceVal[1].city_code;
					}
					if(values[2]!=undefined){	
						console.log(values)
						_this.value3=provinceVal[2].city_name;
						_this.value3Id=provinceVal[2].city_code;
					console.log('shsihi',_this.value1Id,_this.value2Id,_this.value3Id)
					}
					var json={};
					json['cityCode']=provinceVal[0].city_code;
					json['requestUrl']=_this.url;
					json['busId']=_this.busId;
					_this.getCityData(json)
				}
				
		    },
		    closeOverlayer(){
				var _this=this;
				_this.isCheckPopUp=false;
			},
		    cancleaddress(){
		    	var _this=this;
		    	_this.isCheckPopUp=false;
		    	_this.addressShow=false;
		    	if(_this.memberoption.addrOption==0){
		    		_this.value1Id='';
		    		_this.value2Id='';
		    		_this.value3Id='';
		    		_this.addr='';
		    	}else{
		    		_this.tipsMsg(_this.tipMsg.isaddrMsg);
		    	}
		    },
		    selectaddress(){
		    	var _this=this;
		    	_this.isCheckPopUp=false;
		    	_this.addressShow=false;
		    	if(_this.memberoption.addrOption==1){
		    		_this.checkOption.addrOption=true;
					_this.checkPickClub();
		    	}
		    	_this.addr=_this.value1+_this.value2+_this.value3;
		    },
			// 地址详情
			addressdetails(){
				var _this=this;
				if(_this.memberoption.addrDetailOption==1){
					if(_this.addressdetail==''){
						_this.checkOption.addrOption=false;
						_this.tipsMsg(_this.tipMsg.isaddressdetailMsg);
						_this.checkPickClub();
					}else{
						_this.checkOption.addrOption=true;
						_this.checkPickClub();
					}
					
				}
			},
			//检测必填项是否为空
			checkPickClub(){
				var _this=this,isTrue=[],isFalse=[],count = 0;
				for(let i in _this.checkOption){
					count++;
					if(_this.checkOption[i]==false){
						isFalse.push(_this.checkOption[i]);
					}
					if(_this.checkOption[i]==true){
						isTrue.push(_this.checkOption[i]);
					}
				}
				if(count==isTrue.length){
					_this.isPickClub=true;
					return true;
				}else{
					_this.isPickClub=false;
		            return false;
				}
			},
			// 手机号码验证
			verification(){
				var _this=this;
				if(_this.memberoption.phoneOption==1){
					_this.isTrue=Mobilephone(_this.phone);
					if(!_this.isTrue){
						_this.tipsMsg(_this.tipMsg.isphoneMsg);
						_this.checkOption.phoneOption=false;
						_this.checkPickClub();
					}else{
						_this.checkOption.phoneOption=true;
						_this.checkPickClub();
					}
				}
			},
			//手机验证码
			checkAuthCode(){
				var _this=this;
				if(_this.code!=''){
					_this.checkOption.code=true;
					_this.checkPickClub();
				}else{
					_this.tipsMsg(_this.tipMsg.isphoneCodeMsg);
					_this.checkOption.code=false;
					_this.checkPickClub();
				}
			},
			//名字检测
			clickName(){
				var _this=this;
				if(_this.memberoption.nameOption==1){
					_this.isTrue=Lib.M.checkName(_this.name);
					if(_this.isTrue){
						_this.tipsMsg(_this.tipMsg.isnameMsg);
						_this.checkOption.nameOption=false;
						_this.checkPickClub();
					}else{
						_this.checkOption.nameOption=true;
						_this.checkPickClub();
					}
				}
				
			},
			//获取性别
			clickSex(){
				var _this=this;
				_this.isCheckPopUp=true;
				this.isSexSelect=true;
			},
			sureCancelman(){
				var _this=this;
				_this.isSexSelect=false;
				_this.isCheckPopUp=false;
				_this.isColor=true;
				_this.sex=_this.man;
				_this.member.sex=1;
				if(_this.memberoption.sexOption==1){
					_this.checkOption.sexOption=true;
					_this.checkPickClub();
				}
			},
			sureCancelgril(){
				var _this=this;
				_this.isSexSelect=false;
				_this.isCheckPopUp=false;
				_this.sex=_this.gril;
				_this.member.sex=2;
				_this.isColor=true;
				if(_this.memberoption.sexOption==1){
					_this.checkOption.sexOption=true;
					_this.checkPickClub();
				}
			},
			fileCancel(){
				var _this=this;
				_this.isSexSelect=false;
				_this.isCheckPopUp=false;
				_this.sex=_this.secret;
				_this.member.sex=0;
				_this.isColor=false;
				if(_this.memberoption.sexOption==1){
					_this.tipsMsg(_this.tipMsg.issexMsg);
					_this.checkOption.sexOption=false;
					_this.checkPickClub();
				}
			},
			keepSecret(){
				var _this=this;
				_this.isSexSelect=false;
				_this.sex='保密';
				_this.isCheckPopUp=false;
				_this.memberData.sex=0;
				_this.isColor=true;
				if(_this.memberoption.sexOption==1){
					_this.checkOption.sexOption=true;
					_this.checkPickClub();
				}
			},
			//生日选择
			clickBirth(){
				var _this=this;
				_this.isCheckPopUp=true;
				_this.$refs.picker.open();
			},
			checkinCancel(){
				var _this=this;
				_this.isCheckPopUp=false;
				_this.birth='';
				if(_this.memberoption.birthOption==1){
					_this.tipsMsg(_this.tipMsg.isbirthMsg);
					_this.checkOption.birthOption=false;
					_this.checkPickClub();
				}
				_this.$refs.picker.close();
			},
			handleConfirm(value){
				var _this=this;
				_this.isCheckPopUp=false;
				_this.pickerVisible=Lib.M.formatTime(value,true)
				_this.birth=_this.pickerVisible
				if(_this.memberoption.birthOption==1){
					_this.checkOption.birthOption=true;
					_this.checkPickClub();
				}

			},
			
			//年收入
			checkMoney(){
				var _this=this;
				if(_this.memberoption.getMoneyOption==1){
					_this.isTrue=Lib.M.checkNumber(_this.getMoney);
					if(_this.isTrue){
						_this.tipsMsg(_this.tipMsg.isgetMoneyMsg);
						_this.checkOption.getMoneyOption=false;
						_this.checkPickClub();
					}else{
						_this.checkOption.getMoneyOption=true;
						_this.checkPickClub();
					}
				}
			},
			//邮箱
			checkEmail(){
				var _this=this;
				if(_this.memberoption.mailOption==1){
					_this.isTrue=Lib.M.checkEmail(_this.email);
					if(_this.isTrue){
						_this.tipsMsg(_this.tipMsg.isemailMsg);
						_this.checkOption.mailOption=false;
						_this.checkPickClub();
					}else{
						_this.checkOption.mailOption=true;
						_this.checkPickClub();
					}
				}
				
			},
			//身份证
			checkidCard(){
				var _this=this;
				if(_this.memberoption.cardOption==1){
					_this.isTrue=Lib.M.checkidCard(_this.sfCard);
					if(_this.isTrue){
						_this.tipMsg.sfCardMsg='身份证号码错误';
						_this.tipsMsg(_this.tipMsg.sfCardMsg);
						_this.checkOption.cardOption=true;
						_this.checkPickClub();
					}else{
						_this.checkOption.cardOption=false;
						_this.checkPickClub();
					}
				}
			},
			//获取验证码
			getCode(){
				var _this=this;
				var json={};
				_this.isTrue=Mobilephone(_this.phone);
				if(!_this.isTrue){
					_this.tipsMsg(_this.tipMsg.isphoneMsg);
					_this.checkOption.phoneOption=false;
					_this.checkPickClub();
					return false;
				}else{
					_this.checkOption.phoneOption=true;
					_this.checkPickClub();
				}
				_this.isGetCode=true;
				json["busId"]=_this.busId;
				json["phone"]=_this.phone;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.sendMsg_POST,
                    'type': 'POST',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	console.log("phonedata",data);
                    }

				})
				var time=setInterval(function(){
					_this.timeleft--;
					if(_this.timeleft==0){
						_this.isGetCode=false;
						_this.timeleft=120;
						clearTimeout(time);
					}
				},1000);
			},
			tipsMsg(msg){
				var _this=this;
				Toast({
	            	message:msg,
	            	duration:1000
	            });
			},
			//提交数据
			submitData(){
				var _this=this;
				var json={};
				_this.pickerVisible =Lib.M.formatTime(_this.pickerVisible ,true)
				_this.pickerVisible = _this.pickerVisible.replace(new RegExp("-","gm"),"/");
				if(_this.phone==''){
					_this.tipsMsg(_this.tipMsg.isphoneMsg);
					return false;
				}else{
					if(!Mobilephone(_this.phone)){
						_this.tipsMsg(_this.tipMsg.isphoneMsg);
						return false;
					}
				}
				
				if(_this.memberoption.nameShow1==1){
					if(_this.name==''){
						if(_this.memberoption.nameOption==1){
							_this.tipsMsg(_this.tipMsg.isnameMsg);
							return false;
						}
					}else{
						if(Lib.M.checkName(_this.name)){
							console.log(Lib.M.checkName(_this.name))
							_this.tipsMsg(_this.tipMsg.isnameMsg);
							return false;
						}
					}
				}
				if(_this.memberoption.sexShow1==1){
					if(_this.memberoption.sexOption==1){
						if(_this.sex=='选填'){
							_this.tipMsg.issexMsg='性别不能为空';
							_this.tipsMsg(_this.tipMsg.issexMsg);
							return false;
						}
					}
				}
				if(_this.memberoption.birthShow1==1){
					if(_this.birth==''){
						if(_this.memberoption.birthOption==1){
							_this.tipMsg.isbirthMsg='出生日期不能为空';
							_this.tipsMsg(_this.tipMsg.isbirthMsg);
							return false;
						}
					}
				}
				
				if(_this.memberoption.addrShow1==1){
					if(_this.addr==''){
						if(_this.memberoption.addrOption==1){
							_this.tipMsg.isaddrMsg='地址不能为空';
							_this.tipsMsg(_this.tipMsg.isaddrMsg);
							return false;
						}
					}
				}
				if(_this.memberoption.addrDetailShow1==1){
					if(_this.addressdetail==''){
						if(_this.memberoption.addrDetailOption==1){
							_this.tipMsg.isaddressdetailMsg='地址详情不能为空';
							_this.tipsMsg(_this.tipMsg.isaddressdetailMsg);
							return false;
						}
					}
				}
				if(_this.memberoption.getMoneyShow1==1){
					if(_this.getMoney==''){
						if(_this.memberoption.getMoneyOption==1){
							_this.tipMsg.isgetMoneyMsg='年收入不能为空';
							_this.tipsMsg(_this.tipMsg.isgetMoneyMsg);
							return false;
						}
					}else{
						if(Lib.M.checkNumber(_this.getMoney)){
							_this.tipsMsg(_this.tipMsg.isgetMoneyMsg);
							return false;
						}
					}
				}
				if(_this.memberoption.mailShow1==1){
					if(_this.email==''){
						if(_this.memberoption.mailOption==1){
							_this.tipMsg.isemailMsg='邮箱不能为空';
							_this.tipsMsg(_this.tipMsg.isemailMsg);
							return false;
						}
					}else{
						if(Lib.M.checkEmail(_this.email)){
							_this.tipsMsg(_this.tipMsg.isemailMsg);
							return false;
						}
					}
				}
				
				if(_this.memberoption.cardShow1==1){
					if(_this.sfCard==''){
						if(_this.memberoption.cardOption==1){
							_this.tipMsg.issfCardMsg='身份证不能为空';
							_this.tipsMsg(_this.tipMsg.issfCardMsg);
							return false;
						}
					}else{
						if(Lib.M.checkidCard(_this.sfCard)){
						}else{
							_this.tipsMsg(_this.tipMsg.issfCardMsg);
							return false;
						}
					}
				}
				if(_this.sex==='选填'){
					_this.member.sex=0;
				}
				console.log('_this.checkOption',_this.checkOption)
				if(!_this.checkPickClub()){
		            _this.tipsMsg('必填项不能为空');
		            return false;
				};
				json['busId']=parseInt(_this.busId);
				json['phone']=_this.phone;
				json['ctId']=_this.ctId;
				json['gtId']=_this.gtId;
				json['name']=_this.name;
				json['sex']=_this.member.sex;
				json['birth']=_this.birth;
				json['email']=_this.email;
				json['cardId']=_this.sfCard;
				json['provice']=_this.value1Id;
				json['city']=_this.value2Id;
				json['countyCode']=_this.value3Id;
				json['address']=_this.addressdetail;
				json['getMoney']=_this.getMoney;
				json['tuijianCode']=_this.tuijianCode;
				json['code']=_this.code;
				json['payType']=_this.payType;//支付方式   1---微信支付 2---支付宝 
				Lib.M.ajax({
					'url':memberAPI.activeAPI.linquMemberCard_GET,
                    'type': 'GET',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	if(data.code==0){
                    		_this.$router.push({ name: 'home', params: { busId: _this.busId }});
                    	}
                    	// window.location.href=_this.
                    	console.log('tijiaocg',data);
                    },
                    'error':function(err){
                    	Toast({
						  	message: '领取失败',
						  	duration: 3000
						});
                    },
                    'warning':function(res){
                    	console.log('tijiaowaen',res);
                    }
				})
			}
		},
		created:function(){
			this.loadData();
		}

	}
</script>
<style>


.pickclub{
	background: #f3f3f3;
	height: 100%;
}
.pickclub .clubType{
}
.pickclub .clubType .cardType{
	background: #fff;
	text-align: left;
	margin-bottom: 0.2rem;
	padding-bottom:  0.2rem;
}
.pickclub .clubType .cardType p,.pickclub .clubType .cardType h3{
	padding: 0.35rem 0 0.25rem 0.3rem;
	font-size: 0.32rem;
	font-weight: normal;
	text-align: left;
}
.pickclub .clubType .cardType p+div{
	padding-left: 0.94rem;
}
.pickclub .clubType .cardType span{
	height: 0.6rem;
	width: 1.85rem;
	border: 1px solid #29cc6d;
	color: #29cc6d;
	line-height: 0.6rem;
	border-radius: 0.12rem;
	font-size: 0.28rem;
	margin: 0 0 0.25rem 0.3rem; 
	text-align: center;
	float: left;
}
.pickclub .clubType .cardType span:nth-child(2){
	margin-left: 0.7rem;
}
.pickclub .clubType .cardType span:nth-child(5){
	margin-left: 0.7rem;
}
.pickclub .clubType .cardType .authCode,
.pickclub .clubType .cardType .phone{
	border-bottom: 1px solid #f7f3f3;
	position: relative;
	margin:0 0.3rem 0 0.4rem;
	font-size: 0.8rem;
}
.pickclub .clubType .cardType .phone{
	line-height: 0.8rem;
	position: relative;
}
.pickclub .clubType .cardType .phone a{
	border-left: 1px solid #f7f3f3;
	font-size: 0.3rem;
	color: #29cc6d;
	padding-left: 0.2rem;
	position: absolute;
	bottom: 0.1rem;
	right: 0.4rem;
}
.pickclub .clubType .cardType input{
	outline: none;
	border: none;
	font-size: 0.28rem;
	line-height: 0.8rem;
	vertical-align: top;
}
.pickclub .clubType .cardType label{
	font-size: 0.32rem;
	height:0.8rem;
	width: 1.4rem;
	display: inline-block;
	vertical-align: top;
}
.pickclub .clubType .cardType .sex input[type=button]{
	background: #fff;
	text-align: left;
	color:#bfbfbf ;
	width: 50%;
	line-height: 0.76rem;
	vertical-align: top;
}
.pickclub .clubType .cardType .sex .icon{
	font-size: 0.3rem;
	color: #c7c7cc;
	height: 0.7rem;
}
.pickclub .clubType .cardType .sex  span{
	border: 0;
	width: 0.4rem;
	float: right;
	margin: 0.1rem 0.3rem 0 0;
}
#sex{
	padding-left: 0.2rem;
}
#address{
	width: 4.5rem;
	overflow: hidden;
	text-overflow:ellipsis;
	white-space: nowrap;
}
.pickclub .withdrawButton{
	background: #f3f3f3;
	margin: 0;
	font-size: 1.2rem;
}
/*会员卡购买*/
.purchasingCard .IntegralDataComRi a{
	border: 1px solid #29cc6d;
	color: #29cc6d;
	height: 0.6rem;
	width: 1.4rem;
	line-height: 0.6rem;
	text-align: center;
	border-radius: 0.12rem;
	margin:0.4rem 0.25rem 0.4rem 0;
	font-size: 0.28rem;
}
.IntegralDataCom .IntegralDataComRi span {
  color: #29cc6d;
  font-size: 0.3rem;
  vertical-align: middle;
  margin: 0;
}
.pickclub .club .head span,
.pickclub .club .grade .gradeCom p{
	color: #333743 !important;
}
.pickclub .club .clubCard{
	background: #ffd356 !important;
    background: -moz-linear-gradient(left,#fedb70,#fec725);
    background: -webkit-linear-gradient(left,#fedb70,#ffd147,#fec725) !important;
}
.pickclub .clubType .cardType>div:last-child{
	border: 0;
}
</style>


