import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Member/Home'
import TradingRecord from '@/pages/Member/TradingRecord'
import MyMessage from '@/pages/Member/MyMessage'
import OfferPay from '@/pages/Member/OfferPay'
import MemberEquity from '@/pages/Member/MemberEquity'
import ApplyStore from '@/pages/Member/ApplyStore'
import FlowExchange from '@/pages/Recharge/Flow/FlowExchange'
import TopUp from '@/pages/Recharge/Balance/TopUp'
import PowderShop from '@/pages/Recharge/powderShop'
import Integral from '@/pages/Inquire/Integral'
import PowderCurrency from '@/pages/Inquire/PowderCurrency'
import FlowRecord from '@/pages/Inquire/FlowRecord'
import CompleteInfor from '@/pages/CompleteInfor'
import EditProfile from '@/pages/CompleteInfor/EditProfile'
import Recommend from '@/pages/Recommend/Recommend'
import QRCode from '@/pages/Recommend/QRCode'
import WithDraw from '@/pages/Recommend/WithDraw'
import ShowDetails from '@/pages/Recommend/ShowDetails'
import PickClub from '@/pages/PickClub/PickClub'
import MapView from '@/pages/Member/map'

import ShareQRCode from '@/pages/Recommend/ShareQRCode'
import OfferPayOther from '@/pages/Member/OfferPayOther'
import ShareOfferPay from '@/pages/Member/ShareOfferPay'

Vue.use(Router)

var router = new Router({
   // base: '/html/phone/index.html',//添加的地方
  routes: [
    //推荐跳转首页
    {
      path: '/home/:busId/:tuijianCode',
      component: Home,
      meta: {
        title: '会员中心'
      }
    },
    {
      path: '/home/:busId',
      name: 'home',
      component: Home,
      meta: {
        title: '会员中心'
      }
    },
    /*交易记录*/
    {
      path: '/tradingRecord/:busId',
      name: 'tradingRecord',
      component: TradingRecord,
      meta: {
        title: '交易记录'
      }
    },
    /*我的信息*/
    {
      path: '/myMessage/:busId',
      name: 'myMessage',
      component: MyMessage,
      meta: {
        title: '我的信息'
      }
    },
    /*订单支付*/
    {
      path: '/offerpay/:busId',
      name: 'offerpay',
      component: OfferPay,
      meta: {
        title: '向商家支付'
      }
    },
    /*会员权益*/
    {
      path: '/memberequity/:busId',
      name: 'memberequity',
      component: MemberEquity,
      meta: {
        title: '会员权益'
      }
    },
    /*流量兑换*/
    {
      path: '/flowexchange/:busId',
      name: 'flowexchange',
      component: FlowExchange,
      meta: {
        title: '流量兑换'
      }
    },
    /*充值金额*/
    {
      path: '/topup/:busId',
      name: 'topup',
      component: TopUp,
      meta: {
        title: '充值'
      }
    },
    /*适用门店*/
    {
      path: '/applystore/:busId',
      name: 'applystore',
      component: ApplyStore,
      meta: {
        title: '适用门店'
      }
    },
    /*积分查询*/
    {
      path: '/integral/:busId',
      name: 'integral',
      component: Integral,
      meta: {
        title: '积分'
      }
    },
    /*粉币查询*/
    {
      path: '/powdercurrency/:busId',
      name: 'powdercurrency',
      component: PowderCurrency,
      meta: {
        title: '粉币'
      }
    },
    /*流量查询*/
    {
      path: '/flowrecord/:busId',
      name: 'flowrecord',
      component: FlowRecord,
      meta: {
        title: '流量'
      }
    },
    /*完善资料*/
    {
      path: '/completeinfor/:busId',
      name: 'completeinfor',
      component: CompleteInfor,
      meta: {
        title: '完善资料'
      }
    },
    /*完善资料*/
    {
      path: '/editprofile/:busId',
      name: 'editprofile',
      component: EditProfile,
      meta: {
        title: '完善资料'
      }
    },
    /*推荐页面*/
    {
      path: '/recommend/:busId',
      name: 'recommend',
      component: Recommend,
      meta: {
        title: '推荐'
      }
    },
    /*推荐二维码*/
    {
      path: '/qrcode/:busId',
      name: 'qrcode',
      component: QRCode,
      meta: {
        title: '推荐二维码'
      }
    },
    /*提现页面*/
    {
      path: '/withdraw/:busId',
      name: 'withdraw',
      component: WithDraw,
      meta: {
        title: '提现'
      }
    },
    /*提现详情*/
    {
      path: '/showdetails/:busId',
      name: 'showdetails',
      component: ShowDetails,
      meta: {
        title: '提现详情'
      }
    },
    /*推荐领取会员卡*/
    {
      path: '/pickclub/:busId/:tuijianCode',
      component: PickClub,
      meta: {
        title: '领取会员卡'
      }
    },
    /*领取会员卡*/
    {
      path: '/pickclub/:busId',
      name: 'pickclub',
      component: PickClub,
      meta: {
        title: '领取会员卡'
      }
    },
    /*粉币商城*/
    {
      path: '/powderShop/:busId',
      name: 'powderShop',
      component: PowderShop,
      meta: {
        title: '粉币商城'
      }
    },
    /*位置展示*/
    {
      path: '/mapview/:busId',
      name: 'mapview',
      component: MapView,
      meta: {
        title: '位置展示'
      }
    },
    /*分享二维码*/
    {
      path: '/shareQRCode/:busId',
      name: 'shareQRCode',
      component: ShareQRCode,
      meta: {
        title: '分享二维码'
      }
    },
    /*转借他人消费*/
     {
      path: '/offerpayother/:busId',
      name: 'offerpayother',
      component: OfferPayOther,
      meta: {
        title: '转借他人消费'
      }
    },
    /*转借他人消费*/
     {
      path: '/shareOfferPay/:busId',
      name: 'shareOfferPay',
      component: ShareOfferPay,
      meta: {
        title: '转借他人消费'
      }
    }
  ]
});
router.beforeEach((to, from, next) => {

  next()

});
router.afterEach((transition) => {
  header.setTitle(transition.meta.title)
});
export default router;
var header = {
  /**
   * 修改页面标题
   * @param title 标题
   */
  setTitle(title) {
    document.title = title;
    var mobile = navigator.userAgent.toLowerCase()
    if (/iphone|ipad|ipod/.test(mobile)) {
      var iframe = document.createElement('iframe')
      iframe.style.display = 'none'
      var iframeCallback = function() {
        setTimeout(function() {
          iframe.removeEventListener('load', iframeCallback)
          document.body.removeChild(iframe)
        }, 0)
      }
      iframe.addEventListener('load', iframeCallback)
      document.body.appendChild(iframe)
    }
  }
}



