<template>
	<div class="home" v-if="noData">
		<ClubCard></ClubCard>
		<!-- 详细信息 -->
		<div class="ClubDetails clearfix">
			<div class="head clearfix">
				<div class="headImgDiv fl">
					<img class="headImg" :src="memberData.headimgurl" @click="loadData">
				</div>
				<div class=" fl clearfix">
					<p class="nickname ">
						<span class="name">{{memberData.nickname}}</span>
						<span v-if="isDiscount">可享折扣：{{zhuKadiscount}}折</span>
					</p>
					<p class="remainingDay">
						<!--  -->
						<router-link tag="span"  :to="{ name: 'topup',query: {chongzhiCtId:3 }}"  v-if="isRemainMoney">
							<span class="money">余额：<span :class="{maxSpanValue:isMoneyOverFlow}">{{cardData.money}}</span><i class="icon iconfont icon-you"></i></span>
						</router-link>
						<!-- " -->
						<router-link tag="span"  :to="{ name: 'topup',query: { chongzhiCtId:4}}" v-if="isRemainDay">
							<span>剩余：<span :class="{maxSpanValue:isOverFlow}">{{remainDay}}</span>天<i class="icon iconfont icon-you"></i></span>
						</router-link>
						<!-- -->
						<router-link tag="span" :to="{ name: 'topup',query: {chongzhiCtId:5}}"  v-if="isRemainNum">
							<span class="frequency">剩余：<span :class="{maxSpanValue:isCiShuOverFlow}">{{cardData.frequency}}</span>次<i class="icon iconfont icon-you"></i></span>
						</router-link>	
						<router-link tag="span" :to="{ name: 'pickclub',params: { busId: busId }}" v-if="iseasy">
							<span class="wanshan">完善资料正式成为会员<i class="icon iconfont icon-you"></i></span>
						</router-link>	
					</p>
						
				</div>
			</div>
			<div class="grade clearfix">
				<div class="w35 fl">
					<router-link tag="p"  :to="{ name:'integral',params: { busId: busId }}">
						<p><span>{{memberData.integral}}</span>积分</p>
					</router-link>
				</div>
				<div class="w30 fl">
					<router-link tag="p" :to="{ name: 'powdercurrency',params: { busId: busId }}">
						<p><span>{{memberData.fansCurrency}}</span>粉币</p>
					</router-link>
				</div>
				<div class="w30 fl">
					<router-link tag="p" :to="{ name: 'flowrecord',params: { busId: busId }}">
						<span>{{memberData.flow}}M</span>流量
					</router-link>
				</div>
			</div>
			<div class="OfferPay" v-if="isOfferPay">
				<router-link  :to="{ name: 'OfferPay',params: { busId: busId }}" >
					<input class="" type="button" name="" value="优惠买单">
				</router-link>
				<!-- <p>会员积分可抵扣现金</p> -->
			</div>
		</div>
		<!-- 列表信息 -->
		<div class="ClubList clearfix">
			<div class="signin clearfix">
				<router-link tag="p" :to="{ name: 'memberequity',params: { busId: busId }}" class="signin1">
					<p>
						<svg class="icon" aria-hidden="true">
	                        <use xlink:href="#icon-quanyi"></use>
	                    </svg>
	                    <span>会员权益<i class="icon iconfont icon-you"></i></span>
	                </p>
				</router-link>
				<p v-if="userQindao" class="signin2" @click="chickSignPop" v-bind:class="{ selected: signIn }">
					<svg class="icon" aria-hidden="true" v-if="signIn">
                        <use xlink:href="#icon-yiqiandao"></use>
                    </svg>
					<svg class="icon" aria-hidden="true" v-else>
                        <use xlink:href="#icon-qiandao"></use>
                    </svg>
					<span>{{signIn==1?'已签到':'签到'}}<i v-if="signIn" class="icon iconfont icon-gou"></i></span>
				</p>
			</div>
			<div class="information clearfix">
				<router-link tag="p"  :to="{ name: 'completeinfor',params: { busId: busId }}">
					<p class="information1">
						<svg class="icon" aria-hidden="true">
							<use xlink:href="#icon-wanshanziliao"></use>
						</svg>
						<span>完善资料<i class="icon iconfont icon-you"></i></span>
					</p>
				</router-link>
			</div>
			<div class="shopping clearfix">
				<p class="shopping1" @click="unionCard" v-if="webType==1">
					<svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-lianmengqia"></use>
                    </svg>
					<span>我的联盟卡<i class="icon iconfont icon-you"></i></span>
				</p>
				<!-- <p class="shopping2">
					<svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-jifenshangcheng"></use>
                    </svg>
                    <span>积分商城<i class="icon iconfont icon-you"></i></span>
                </p> -->
			</div>
			<div class="discount clearfix">
				<p class="discount1">
					<svg class="icon" aria-hidden="true">
                        <use xlink:href="#icon-youhuiquan"></use>
                    </svg>
					<span>优惠券<i class="icon iconfont icon-you"></i></span>
				</p>
				<router-link tag="p" :to="{ name: 'applystore',params: { busId: busId }}">
					<p class="discount2">
						<svg class="icon" aria-hidden="true">
	                        <use xlink:href="#icon-shiyongmendian"></use>
	                    </svg>
						<span>适用门店<i class="icon iconfont icon-you"></i></span>
					</p>
				</router-link>
				<router-link tag="p" :to="{ name: 'myMessage',params: { busId: busId }}">
					<p class="discount3">
						<svg class="icon" aria-hidden="true">
	                        <use xlink:href="#icon-wodexiaoxi"></use>
	                    </svg>
						<span>我的消息<sup v-show="count" class="icon iconfont icon-weiduhongdian"></sup><i class="icon iconfont icon-you"></i></span>
					</p>
				</router-link>
			</div>
			<div class="recommend clearfix">
				<router-link tag="p"  :to="{ name: 'tradingRecord',params: { busId: busId }}">
					<p class="recommend1 ">
						<svg class="icon" aria-hidden="true">
	                        <use xlink:href="#icon-jiaoyijilu"></use>
	                    </svg>
                    	<span>交易记录<i class="icon iconfont icon-you"></i></span>
                    </p>
				</router-link>
				<router-link tag="p" :to="{ name: 'recommend',params: { busId: busId }}">
					<p class="recommend2">
						<svg class="icon" aria-hidden="true">
	                        <use xlink:href="#icon-tuijian"></use>
	                    </svg>
						<span>推荐<i class="icon iconfont icon-you"></i></span>
					</p>
				</router-link>
				
			</div>
		</div>
		<div v-show="isSignPop">
			<SignExchange :titleCom='titleCom' :titleComTow='titleComTow' :icon="hasIcon"></SignExchange>
		</div>
		<div class="offerPayWindow" v-if="cardNoCheck">
			<div class="offerPayWindowCom blacklist">
				<p>提示</p>
				<p>{{blacklistMsg}}</p>
				<!-- <div class="offerPayButo">
					<p @click="blacklist">关闭</p>
				</div> -->
			</div>
		</div>
		<ClubFoot></ClubFoot>
	</div>
	<!-- <div v-else >
		<div style="line-height: 6rem;font-size: 0.5rem">
			{{msg}}
		</div>
	</div> -->
</template>
<script>
	import SignExchange from "../../components/SignExchange"; 
	import ClubCard from "@/pages/Member/ClubCard";
	import ClubFoot from "../../components/ClubFoot";
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
			    isLoading: false,
			    busId:42,
			    url:'',
			    msg:'',
			    noData:false,
			    isSignPop: false,
			    isSelected:false,
			    isOfferPay:false,
			    zhuKadiscount:0,
			    webType:0,
			    cardNoCheck:false,
			    blacklistMsg:'',

			    isRemainMoney:false,
			    isRemainNum:false,
			    isDiscount:false,
			    isRemainDay:false,
			    iseasy:false,

			    signIn:0,
			    qiandao:0,
			    qindaoJifen:0,
			    userQindao:1,
			    count:0,
			    unionUrl:'',
			    yiqiandao:false,
			    
			   
			    remainDay:1,
			    maxValue:999999,//最大显示数据超出省略号显示
			    isOverFlow:false,
			    isCiShuOverFlow:false,
			    isMoneyOverFlow:false,
			    titleCom:'签到成功',
			    titleComTow:'+10积分',
			    hasIcon:true,
			    memberData:[],
			    cardData:[],
			    fuCardListData:[]

			}
		},
		components:{
		    ClubFoot,
		    ClubCard,
		    SignExchange
		},
		methods:{
			loadData:function(){
				var _this=this;
				
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				
				var json={};
				var nowData=new Date().getTime();
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findMember_GET,
                    'type': 'GET',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	console.log('data',data);
                    	_this.noData=true;
                    	if(data.code==0){
                    		_this.cardData=data.data.card;
                    		if(_this.cardData.cardStatus==1){
                    			_this.cardNoCheck=true;
                    			_this.blacklistMsg='你的会员权益已被限制，如有疑问请联系商家'
                    			return false;
                    		}
                    		if(_this.cardData.isChecked ==0){
                    			_this.cardNoCheck=true;
                    			_this.blacklistMsg='您的会员卡商家正在审核中，请耐心等待';
                    	 		return false;
                    		}
                    		_this.memberData=data.data.member;
                    		_this.zhuKadiscount=data.data.zhuKadiscount;
                    		_this.userQindao=data.data.userQindao;
                    		_this.signIn=data.data.qiandao;
                    		_this.webType=data.data.webType;
                    		_this.unionUrl=data.data.unionUrl;

                    		_this.iseasy=_this.cardData.iseasy;
                    		_this.qindaoJifen=data.data.qindaoJifen;

                    		_this.fuCardListData=data.data.fuCardList;
                    		_this.count=data.data.count;
                    		// 时效卡天数计算
                    		if(_this.cardData.expireDate!=undefined){	
                    			_this.remainDay=Math.ceil((_this.cardData.expireDate-nowData)/(24*60*60*1000));
                    			if(_this.remainDay<0){
	                    			_this.remainDay=0;
	                    		}
                    		}else{
                    			_this.remainDay=0;
                    		}
                    		// 大于1百万的时候数据省略号显示
                    		if(_this.remainDay>=_this.maxValue){
                    			_this.isOverFlow=true;
                    		}
                    		// _this.cardData.money=_this.maxValue
                    		if(_this.cardData.money>=_this.maxValue){
                    			_this.isMoneyOverFlow=true;
                    		}
                    		// _this.cardData.frequency=_this.maxValue
                    		if(_this.cardData.frequency>=_this.maxValue){
                    			_this.isCiShuOverFlow=true;
                    		}
                    		switch(_this.cardData.ct_id){
                    			case 1://积分卡
                    				// _this.isRemainMoney=true;
                    				// _this.isRemainNum=true;
                    				break;
                    			case 2://折扣卡
                    				_this.isDiscount=true;
                    				break;
                    			case 3://储值卡
                    				_this.isRemainMoney=true;
                    				break;
                    			case 4://实效卡
                    				_this.isRemainDay=true;
                    				break;
                    			case 5://次卡
                    				_this.isRemainNum=true;
                    				break;
                    		}
                    		if(_this.fuCardListData!=undefined){
                    			if(_this.fuCardListData.length>0||_this.fuCardListData!=null){
	                    			for(let i=0;i<_this.fuCardListData.length;i++){
	                    				switch(_this.fuCardListData[i]){
			                    			case 1://积分卡
			                    				// _this.isRemainMoney=true;
			                    				// _this.isRemainNum=true;
			                    				break;
			                    			case 2://折扣卡
			                    				_this.isDiscount=true;
			                    				if(data.data.fuKadiscount!=undefined||data.data.fuKadiscount!=''){
			                    					_this.zhuKadiscount=data.data.fuKadiscount;
			                    				}
			                    				break;
			                    			case 3://储值卡
			                    				_this.isRemainMoney=true;
			                    				break;
			                    			case 4://实效卡
			                    				_this.isRemainDay=true;
			                    				break;
			                    			case 5://次卡
			                    				_this.isRemainNum=true;
			                    				break;
			                    		}
	                    			}
	                    		}
                    		}
                    	}
                    },
                   "error":function(){
                   		_this.noData=false;
                   		_this.isSignPop=false;
                   		_this.titleCom="数据链接失败！！！"
                   		if (_this.isSignPop) {
				        	setTimeout(() => {
				          		_this.isSignPop = false;
				          		_this.hasIcon=true;
				        	}, 2000);
				      	}
                   },
                   "warning":function(data){
                   	console.log(data);
                   		_this.noData=false;
                   		if(data.code==5001){
                   			_this.msg=data.msg;
                   			console.log(_this.msg);
                   		}
								
					}
				})
			},
			blacklist(){

			},
			chickSignPop(){
				var _this=this;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				_this.titleComTow='+'+_this.qindaoJifen+'积分';
				if(_this.signIn==1||_this.yiqiandao){
					return false;
				}
				_this.yiqiandao=true;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.qiandao_POST,
                    'type': 'POST',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	console.log('qiandao',data);
                    	_this.yiqiandao=false;
                    	if(data.code==0){
                    		_this.isSignPop=true;
                    		_this.signIn=1;
                    		if (_this.isSignPop) {
					        	setTimeout(() => {
					          		_this.isSignPop = false;
					        	}, 1000);
					        }

                    	}
                    }
				})
				
			},
			unionCard(){
				var _this=this;
				window.location.href=_this.unionUrl;
			}
		},
        created:function(){
            this.loadData();

        }
	}
</script>
<style scope>

	
</style>


