<template>
	<div class="withdraw">
		<div class="clearfix topuphead">
			<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
			<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
				<a >首页</a>
			</router-link>
		</div>
		<div class="withdrawCom" v-if="">
			<div class="withdrawComDetil">
				<!-- <p class="title">到账微信号<span>Wechat 0001</span></p> -->
				<div class="money">
					<span>提现金额</span>
					<p>
						<label>¥</label>
						<input type="number" name="" placeholder="请输入提取金额" v-model="money" @keyup="inputValue">
					</p>
				</div>
				<p class="balances">可提现金额¥<span>{{totalMoney}}</span>，<span @click="withdrawMoney('all')">全部提现</span></p>
				<div class="withdrawButton">
					<button  :class="[isTrue?'active':'buttonDisable']" @click="withdrawMoney" name="" value="" >提现</button>
				</div>
			</div>

		</div>

		<ClubFoot></ClubFoot>
	</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import Lib from '@/assets/js/Lib';
	import { Toast } from 'mint-ui';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	totalMoney:50,
		    	money:'',
		    	isTrue:false,
		    	istijiao:false
			}
		},
		components:{
		    ClubFoot
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				_this.busId= _this.$route.params.busId;
				_this.totalMoney= _this.$route.params.totalMoney;
				_this.url=window.location.href;
			},
			inputValue(){
				var _this=this;
				if(_this.money<=_this.totalMoney){
					_this.isTrue=true;
				}
				if(_this.money>_this.totalMoney||_this.money==''||_this.money==0){
					_this.isTrue=false;
				}
				
			},
			withdrawMoney(pos){
				var _this=this,json={};
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				if(pos=='all'){
					_this.isTrue=true;
					_this.money=_this.totalMoney
					if(_this.money==0){
						_this.isTrue=false;
					}
				}
				if(!_this.isTrue){
					Toast({
						message: '提现金额大于零小于等于可提现金额',
					  	duration: 2000,
					  	className:'payStyle'
					})
					return false;
				}
				if(_this.istijiao){
					return false;
				}
				_this.istijiao=true;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json["money"]=_this.money;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.pickMoney_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	_this.istijiao=false;
	                	if(data.code==0){
	                		_this.$router.push({ name:'showdetails',params: { busId: _this.busId }})
	                	}
	                	console.log('pickMoney',data)
	                },
	                'error':function(err){
	                	_this.istijiao=false;
	                	Toast({
							message: err.msg,
						  	duration: 2000
						})
	                }
				})
			}
		},
		created:function(){
	    	var _this=this;
	    	_this.loadData();  
	    }

	}
</script>
<style>
.withdraw .topuphead a{
	color: #333;
}
.withdraw .withdrawCom .withdrawComDetil{
	background: #fff;
	border-radius: 8px;
	/*margin:0.4rem 0.2rem 2rem 0.2rem;*/
}
.withdraw .withdrawCom .withdrawComDetil p{
	text-align: left;
	font-size: 1rem;
}
.withdraw .withdrawCom .withdrawComDetil .title{
	height:1rem;
	background: #fbfbfb;
	border-radius:12px ;
	color: #333;
	font-size: 0.28rem;
	padding-left: 0.35rem;
	line-height: 1rem;
}
.withdraw .withdrawCom .withdrawComDetil .title span{
	padding-left:0.4rem;
	color: #576d9e;
	font-size: 0.28rem;
}
.withdraw .withdrawCom .withdrawComDetil .money{
	font-size: 0.8rem;
	text-align: left;
	margin:  0.2rem 0.2rem 0.2rem 0.5rem;
	border-bottom: 1px solid #e5e5e5;
}
.withdraw .withdrawCom .withdrawComDetil .money span{
	
	font-size: 0.28rem;
}
.withdraw .withdrawCom .withdrawComDetil .money p label{
	font-size: 0.54rem;
	line-height: 0.8rem;
    height: 0.8rem;
    vertical-align: top;
    display: inline-block;
}
.withdraw .withdrawCom .withdrawComDetil .money p input{
	font-size: 0.88rem;
	outline: none;
	border: none;
	width: 5rem;
	margin-left: 10px;
	line-height: 1rem;
    height: 1rem;
    vertical-align: top;
}
.withdraw .withdrawCom .withdrawComDetil .money input::-webkit-input-placeholder {
	font-size: 0.28rem;
	line-height: normal;
	line-height: 1rem;
}
.withdraw .withdrawCom .withdrawComDetil .balances{
	margin: 0.3rem 0 0 0.5rem;
	font-size: 0.24rem;
	color: #888;
}
.withdraw .withdrawCom .withdrawComDetil .balances span{
	font-size: 0.24rem;
}
.withdraw .withdrawCom .withdrawComDetil .balances span:last-child{
	color: #576b9e;
}
.withdraw .withdrawCom .withdrawButton{
	/*padding:  0.2rem 0 0rem 0.2rem;*/
	font-size: 1rem;
	background: none;
	margin-top: 0.2rem;
}
.withdraw .withdrawCom .withdrawButton button{
	margin: 0;
}
.payStyle{
	width: 60%;
}
</style>


