<template>
		<div class="Topup flowexchange">
			<div class="Topuptitle">
				<div class="clearfix topuphead">
					<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
					<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
						<a >首页</a>
					</router-link>
				</div>
				<p>您当前流量为</p>
				<p class="residue">{{flow}}<span>M</span></p>
			</div>
			<div class="Topupoptions">
				<div class="options clearfix">
					<p id="a" class="optionTitle">选择流量包<span>(以下均为全国三网通用流量)</span></p>
					<!--:disabled="(item.count)===0||(item.type>flow)" -->
					<div  class=" fl choose" 
					v-for="(item,index) in busUserFlowData" :class='[[((index+1)===isChoose)?"active":"noactive"],{noselect:(item.count)===0||(item.type>flow)}]'  @click="chooseMen(index+1)" 
					>
						<p>
							<span>{{item.type}}M</span>
							<span v-if="item.count==0">已兑完</span>
							<span v-if="(item.type>flow)&&(item.count>0)">不可兑</span>
						</p>
					</div>
				</div>
				<div class="paySubmit flowButo">
					<input :class="{ active: isExchange }" id="isExchange" type="button" name="" @click="exchangeFlow(1)" value="兑换">
					<input :class="{ active: isExchangeGive }"  type="button" name="" @click="exchangeGive " value="赠送">
				</div>
				<ClubFoot></ClubFoot>
			</div>
			<!-- 转赠手机号弹出窗 -->
			<transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
				<div class="phoneWindow" v-if="phoneWindowShowGive">
					<div class="phoneWindowCom">
						<p>请输入要转赠的手机号</p>
						<p>流量一旦赠送将不予退换</p>
						<input :class="{isphone:isPhone}" type="number" v-model="givePhone"  @keyup="checkPhone" placeholder="请填写手机号码" name="">
						<p v-show="!isPhone" class="phonemsg">手机号码不正确</p>
						<div>
							<span @click="cancel">取消</span>
							<span @click="exchangeFlow(2)" id="isExchangeGive">赠送</span>
						</div>
					</div>
				</div>
			</transition>
			<div v-if="isExchangePop">
				<SignExchange :titleCom="titleCom" :icon="hasIcon"></SignExchange>
			</div>
			<!-- <div v-if="isGivePop">
				<SignExchange titleCom="赠送成功" ></SignExchange>
			</div> -->
		</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import SignExchange from "@/components/SignExchange"; 
	import 'animate.css/animate.css';
	import Lib from '@/assets/js/Lib';
	import { Toast } from 'mint-ui';
	export default{
		data() {
		    return {
		    	isChoose:0,
		    	isExchange:true,
		    	isExchangeGive:false,
		    	isChecked:false,
			    isExchangePop:false,
			    isGivePop:false,
			    busId:0,
			    url:'',
			    flow:0,
			    phone:'',
			    isPhone:false,
			    givePhone:'',
			    hasIcon:true,
			    titleCom:'',
			    pitchUpData:[],
			    phoneWindowShowGive:false,
			    busUserFlowData:[]
			}
		},
		components:{
		    ClubFoot,
		    SignExchange
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findBusUserFlow_GET,
	                'type': 'GET',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	if(data.code==0){
	                		_this.busUserFlowData=data.data.busFlows;
	                		if(data.data.memberFlow==null){
	                			data.data.memberFlow=0;
	                		}
	                		_this.flow= data.data.memberFlow;
	                		_this.phone=data.data.phone;
	                	}
	                	console.log('flowEchange',data);

	                	
	                }
				})
			},
			chooseMen(index){
				var _this=this;
				if(_this.pitchUpData.type>_this.flow||_this.pitchUpData.count==0){
					return false
				}
				if(index==_this.isChoose){
					_this.isChoose=false;
					_this.pitchUpData=[];
				}else{
					_this.isChoose= index;
					_this.pitchUpData=_this.busUserFlowData[index-1];
				}
			},
			exchangeGive(){
				var _this=this,isCorrect;
				_this.isPhone=true;
				isCorrect=_this.checkFlowEmpty();
				if(!isCorrect){
					return false;
				}
				_this.phoneWindowShowGive=true;
				_this.isExchangeGive=true;
				_this.isExchange=false;
			},
			cancel(){
				var _this=this;
				_this.givePhone='';
				_this.phoneWindowShowGive=false;
				_this.isPhone=false;
			},
			checkPhone(){
				var _this=this;
				_this.isPhone=Mobilephone(_this.givePhone);
			},
			checkFlowEmpty(){
				var _this=this;
				if(_this.flow==0||_this.flow<_this.pitchUpData.type){
					_this.titleCom="流量不足!";
					_this.hasIcon=false;
					_this.isExchangePop=true;
					if (_this.isExchangePop) {
			        	setTimeout(() => {
			          		_this.isExchangePop = false;
			          		_this.hasIcon=true;
			        	}, 2000);
			      	}
			      	return false;
				}
				if(_this.pitchUpData.type==undefined){
					_this.titleCom="请选择需要兑换的流量!";
					_this.hasIcon=false;
					_this.isExchangePop=true;
					if (_this.isExchangePop) {
			        	setTimeout(() => {
			          		_this.isExchangePop = false;
			          		_this.hasIcon=true;
			        	}, 2000);
			      	}
			      	return false;
				}else{
					return true;
				}
			},
			exchangeFlow(pos){
				var _this=this;
				var json={},phone,isCorrect;
				if(_this.isChecked){
					return false;
				}
				if(pos==1){
					isCorrect=_this.checkFlowEmpty();
					_this.isExchange=true;
					_this.isExchangeGive=false;
					if(!isCorrect){
						return false;
					}
					phone=_this.phone;
					document.getElementById('isExchange').disabled=true;
				}
				if(pos==2){
					_this.isPhone=Mobilephone(_this.givePhone);
					_this.isExchange=false;
					_this.isExchangeGive=true;
					if(!_this.isPhone){
						return false;
					}
					phone=_this.givePhone;
					_this.isChecked=true;
				}
				
				json["busId"]=_this.busId;
				json["prizeCount"]=_this.pitchUpData.type;
				json["phone"]=phone;
				console.log(json)
				Lib.M.ajax({
					'url':memberAPI.activeAPI.changeFlow_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('phone',data)
	                	document.getElementById('isExchange').disabled=false;
	                	_this.isChecked=false;
	                	if(data.code==0){
							_this.isExchangePop=true;
							if (_this.isExchangePop) {
					        	setTimeout(() => {
					          		_this.isExchangePop = false;
					        	}, 2000);
					      	}
					      	
	                	}
	                },
			      	'warning':function(err){
			      		_this.phoneWindowShowGive=false;
			      		Toast({
			            	message:err.msg+',充值失败',
			            	duration:1000
			            }); 
			            document.getElementById('isExchange').disabled=false;
			            _this.isChecked=false;

			      	}
				})
				
			}
		},
		created:function(){
            this.loadData();
        },
        mounted() {
		  	// const s = document.createElement('script');
		  	// s.type = 'text/javascript';
		  	// s.src = 'http://maint.duofriend.com/upload/common/gt_public_js/public/phone.js';
		  	// document.body.appendChild(s);
		  	
		 }

	}
</script>
<style scope>
	.flowexchange .Topuptitle .residue{
		font-size: 0.9rem;
	}
	.flowexchange .Topuptitle .residue span{
		font-size: 0.3rem;
	}
	.flowexchange .Topupoptions .options p{
		font-size: 0.3rem;
		text-align: center;
		/*min-width: 2rem;*/

	}
	.flowexchange .Topupoptions .options .optionTitle{
		padding: 0.38rem 0 0.3rem 0.24rem;
		text-align: left;
	}
	.flowexchange .Topupoptions .options span{
		font-size: 0.24rem;
		color: #666666;
	}

	.flowexchange .Topupoptions .options .choose{
		height: 1.18rem;
		border: none;
		border:0.02rem solid #29cc6d;
		background: #fff;
		margin-left: 0.18rem; 
		margin-bottom: 0.3rem;
		border-radius: 12px;
		outline: none;
		width: 30%;
		display: block;
	}
	.flowexchange .Topupoptions .options .choose span{
		font-size: 0.4rem;
		min-width: 1.5rem;
		float: left;
		width: 100%;
		color: #29cc6d;
	}
	.flowexchange .Topupoptions .options .choose p span:only-child{
		height: 1rem;
		line-height:0.9rem;
		margin-top: 0;
	}
	
	.flowexchange .Topupoptions .options .choose span+span{
		margin-top: 0px;
		font-size: 0.36rem;
		min-width: 1.5rem;
	}
	.flowexchange .Topupoptions .options .choose.noselect span{
		color: #c5c5c5;
	}
	.flowexchange .Topupoptions .options .choose.active span{
		color: #fff;
	}
	/* 弹出框样式 */
	.flowexchange .phoneWindow{
		position: fixed;
		top:0px;
		width: 100%;
		height: 100%;
		background: rgba(3,3,3,0.6);
	}
	.flowexchange .phoneWindow .phoneWindowCom{
		position: relative;
		background: #fff;
		top:30%;
		width:  75%;
		margin: 0 auto;
		border-radius: 12px;
		font-size: 1rem;
	}
	.flowexchange .phoneWindow .phoneWindowCom p:first-child{
		padding: 0.45rem 0.8rem 0.1rem 0.8rem;
		font-size: 0.32rem;
		font-weight: bold;
	}
	.flowexchange .phoneWindow .phoneWindowCom p:first-child+p{
		color: #989898;
		font-size: 0.26rem;
	}
	.flowexchange .phoneWindow .phoneWindowCom input{
		border: 1px solid #b1b1b1;
		height:0.7rem;
		width: 4.86rem;
		font-size: 0.28rem;
		outline: none;
		padding-left: 8px;
		border-radius: 8px;
		/*margin-bottom: 0.7rem;*/
	}
	.flowexchange .phoneWindow .phoneWindowCom input.iserror{
		margin-bottom: 0px;
	}

	.flowexchange .phoneWindow .phoneWindowCom div{
		border-top: 1px solid #dbdcdd;
		height: 1rem;
	}
	.flowexchange .phoneWindow .phoneWindowCom div span{
		height: 1rem;
		line-height: 1rem;
		width: 40%;
		letter-spacing: 2px;
		font-size: 0.32rem;
		vertical-align: top;
	}
	.flowexchange .phoneWindow .phoneWindowCom div span:last-child{
		color:#40d27d;
		border-left: 1px solid #dbdcdd;
	}
	.isphone{
		margin-bottom: 0.5rem;
	}
</style>


