<template>
		<div class="Topup Integral">
			<div class="conentHeight">
				<div class="Topuptitle">
					<div class="clearfix topuphead">
						<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
						<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
							<a >首页</a>
						</router-link>
					</div>
					<p>当前积分</p>
					<p class="residue">{{jifen}}<span></span></p>
				</div>
				<div class="Topupoptions">
					<div class="options clearfix">
						<!-- 有数据 -->
						<div class="IntegralData" v-if="hasData">
							<ul
							  v-infinite-scroll="loadMore"
							  infinite-scroll-disabled="loading"
							  infinite-scroll-distance="10">
							  <li class="IntegralDataCom clearfix"  v-for="(item,index) in jfData">
							  		<div class="IntegralDataComLe w60 fl">
										<p >{{item.itemName}}</p>
										<p><a href="">{{item.createDate}}</a></p>
									</div>
									<div class=" IntegralDataComRi fr w30 ">
										<span>{{item.number}}积分</span>
									</div>
							  </li>
							</ul>
							<p v-show="isMsg" style="font-size: 0.3rem;padding:  0.2rem" :class="{msgType:loading}">{{msg}}</p>
							<p v-show="!isMsg" style="padding:  0.2rem"></p>
							<div v-if="hasData">
								<ClubFoot ></ClubFoot>
								<div class="buttonHeight" v-if="isNoButo"></div>
							</div>
						</div>
						<!-- 没有数据 -->
						<NoMessage v-else messageCom="暂无积分记录" :class="{integralNoB:!isHied}"></NoMessage>
					</div>
					<div class="integralButton" v-if="isNoButo">
						<button class="active" type="button" @click="jfduihuan" name="" value="" >积分兑换</button>
					</div>
				</div>
			</div>
			<div v-if="!hasData">
				<ClubFoot ></ClubFoot>
			</div>
		</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import NoMessage from "@/components/NoMessage";
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	hasData:false,
		    	loading:false,
		    	busId:0,
		    	isHied:false,
		    	isNoButo:false,
		    	msg:'>>>加载数据<<<',
		    	isMsg:false,
		    	url:'',
		    	page:1,
		    	jifen:0,
		    	jfData:[],
		    	pageData:[],
		    	jfUrl:''
			}
		},
		components:{
		    ClubFoot,
		    NoMessage
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['page']=_this.page;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findCardrecordNewJifen_GET,
	                'type': 'GET',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('jifei',data)
	                	if(data.code==0&&data.data.page.length>0){
	                		_this.jfData=data.data.page;
	                		_this.hasData=true;
	                		_this.pageData=_this.jfData;
	                		_this.jfUrl=data.data.jifenUrl;
	                	}
	                	_this.jifen=data.data.jifen;
	                }
				})
			},
			loadMore() {
				var _this=this;
				_this.loading = true;
				if(_this.jfData.length>=10){
					_this.isMsg=true;
				}
				if(_this.pageData!=0){
			  		_this.page+=1;
			  	}else{
			  		_this.msg='>>>加载完成<<<';
			  		setTimeout(function(){
			  			_this.isMsg=false;
			  		},1000)
            		
			  		return false;
			  	}
			  	var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['page']=_this.page;
				setTimeout(() => {
			  		Lib.M.ajax({
						'url':memberAPI.activeAPI.findCardrecordNewJifen_GET,
		                'type': 'GET',
		                'data':{
		                   "json":json
		                },
		                'success':function (data){
		                	_this.pageData=[];
		                	if(data.data.page.length!=0){
		                		if(data.code==0&&data.data.page.length>0){
			                		_this.pageData=data.data.page;
			                		for(let i=0;i<_this.pageData.length;i++){
			                			_this.jfData.push(_this.pageData[i]);
			                		}
			                	}
		                	}
		                	_this.loading = false;
		                }
					})
				}, 1000);
			},
			jfduihuan(){
				var _this=this;
				window.location.href=_this.jfUrl;
			}
		},
		created:function(){
            this.loadData();

        }

	}
</script>
<style scope>
/*.Integral .Topupoptions .IntegralDataComLe p:first-child{
	margin-top: 0.62rem;
}*/

</style>


