<template>
		<div class="memberEquity">
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="memberEquityDiv" v-if="hasData">
				<div class="memberEquityCom clearfix" v-for="(item,index) in equitiesData" >
					<div class="fl dengji" :class="'dengji' + (index+1)">
						<i class="icon iconfont icon-3"></i>
					</div>
					<div class="fl  grade">
						<p>{{item.gt_grade_name}}</p>
						<p v-if="index==0">完成会员注册，即成为{{item.gt_name}}会员</p>
						<p v-if="index!=0">{{cardType}}达到{{item.gr_upgradeCount}}{{cardUnit}}，即成为{{item.gt_name}}会员</p>
						
					</div>
				</div>
				<div class="memberEquityCom clearfix">
					<div class="fl dengji dengji4">
						<i class="icon iconfont icon-quanyizhanshi"></i>
					</div>
					<div class="fl  grade">
						<p>会员权益</p>
						<p v-html="grEquities"></p>
					</div>
				</div>
			</div>
			<ClubFoot></ClubFoot>
		</div>
</template>
<script>
	import ClubFoot from "@/components/ClubFoot";
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
			    isLoading: false,
			    busId:0,
			    url:'',
			    hasData:true,
			    equitiesData:[],
			    cardType:'积分',
			    cardUnit:'',
			    grEquities:''
			}
		},
		components:{
		    ClubFoot
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={}, ctid;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findMemberEquities_GET,
	                'type': 'GET',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('Equities',data);
	                	if(data.code==0){
	                		ctid=data.data.giveRules[0].ct_id;
	                		if(data.data.giveRules[0].gr_equities!=undefined||data.data.giveRules[0].gr_equities!=''){
                				_this.grEquities=data.data.giveRules[0].gr_equities;
                			}
	                		switch (ctid){
	                			case 1://积分卡
                    				_this.equitiesData=data.data.giveRules;
                    				break;
                    			case 2://折扣卡
                    				_this.equitiesData=data.data.giveRules;
                    				if(_this.equitiesData.length!=0||_this.equitiesData!=undefined){
                    					_this.equitiesData[0].gr_upgradeType==0?(_this.cardType='消费金额'):(_this.cardType='积分');
                    					_this.equitiesData[0].gr_upgradeType==0?(_this.cardUnit='元'):(_this.cardUnit='');
                    				}
                    				break;
                    			case 3://储值卡
                    				_this.equitiesData=data.data.giveRules;
                    				if(_this.equitiesData.length!=0||_this.equitiesData!=undefined){
                    					_this.equitiesData[0].gr_upgradeType==0?(_this.cardType='消费金额'):(_this.cardType='积分');
                    					_this.equitiesData[0].gr_upgradeType==0?(_this.cardUnit='元'):(_this.cardUnit='');
                    				}
                    				break;
                    			case 4://实效卡
                    				_this.equitiesData=data.data.giveRules;
                    				if(_this.equitiesData.length!=0||_this.equitiesData!=undefined){
                    					_this.cardType='充值';
                    					_this.cardUnit='元';
                    				}
                    				break;
                    			case 5://次卡
                    				_this.equitiesData=data.data.giveRules;
                    				if(_this.equitiesData.length!=0||_this.equitiesData!=undefined){
                    					_this.equitiesData[0].gr_upgradeType==0?(_this.cardType='消费次数'):(_this.cardType='积分');
                    					_this.equitiesData[0].gr_upgradeType==0?(_this.cardUnit='次'):(_this.cardUnit='');
                    				}
                    				break;
	                		}
	                		
	                		_this.hasData=true;
	                	}
	                }
				})
			}
		// 	loadMore() {
		// 		var _this=this;
		// 		_this.loading = true;
		// 		if(_this.pageData!=0){
		// 	  		_this.page+=1;
		// 	  	}else{
		// 	  		return false;
		// 	  	}
		// 	  	var json={};
		// 		json["busId"]=_this.busId;
		// 		json["requestUrl"]=_this.url;
		// 		json['page']=_this.page;
		// 		setTimeout(() => {
		// 	  		Lib.M.ajax({
		// 				'url':memberAPI.activeAPI.findMemberEquities_GET,
		//                 'type': 'GET',
		//                 'data':{
		//                    "json":json
		//                 },
		//                 'success':function (data){
		//                 	console.log(data.data.page.length);
		//                 	_this.pageData=[];
		//                 	if(data.data.page.length!=0){
		//                 		if(data.code==0&&data.data.page.length>0){
		// 	                		_this.pageData=data.data.page;
		// 	                		for(let i=0;i<_this.pageData.length;i++){
		// 	                			_this.recordNewData.push(_this.pageData[i]);
		// 	                		}
		// 	                	}
		//                 	}else{
		//                 	}
		//                 	_this.loading = false;
		//                 }
		// 			})
		// 		}, 1000);
		// 	}
		},
		created:function(){
            this.loadData();

        }
	}
</script>
<style scope>
	.memberEquity{
		background: #fff;
	}
	.memberEquity .topuphead{
		background: #f6f6f6;
	}
	.memberEquity .topuphead a{
		color: #333;
	}
	.memberEquity .memberEquityCom{
		text-align: left;
		background: #f6f6f6;
		margin:0.32rem 0.2rem 0.36rem 0.2rem;
		border-radius: 12px ;
		vertical-align: middle;
		min-height: 1.8rem;
	}
	.memberEquity .memberEquityCom .dengji{
		margin:0.25rem 0.15rem 0 0.25rem;
		width:1.3rem;
		height: 1.3rem;
		border-radius: 0.65rem;
		text-align: center;
		font-size: 1rem;
	}
	.dengji1{
		background: #fec64e;
	}
	.dengji2{
		background: #47ccff;
	}
	.dengji3{
		background: #f69c36;
	}
	.dengji4{
		background: #d6af5b;
	}
	.memberEquity .memberEquityCom .dengji .icon{
		font-size: 0.7rem;
		color: #fff;
	}
	.memberEquity .memberEquityCom .grade{
		margin:0;
		width: 75%;
	}
	
	.memberEquity .memberEquityCom .grade p:last-child{
		font-size: 0.26rem;
		padding-bottom: 0.2rem;
	}

	.memberEquity .memberEquityCom .grade p:first-child{
		font-size: 0.3rem;
		margin: 0.45rem 0 0.1rem 0;
		padding-bottom: 0;
	}

	.memberEquity .memberEquityCom .grade p>*{
		font-size: 0.26rem !important;
		margin: 0 !important;
	}
	.memberEquity .memberEquityCom .grade p{
		font-size: 0.3rem;
	}
	
</style>


