<template>
	<div class="completeinfor">
		<div class="bghead">
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
		</div>
		<div class="completeinforComHeight" >
			<div class="clearfix completeinforCom" >
				<div class="clearfix completeinforTit">
					<p class="fl w30">完善个人资料</p>
					<p class="fr w30" @click="EditProfile">
						<i class="icon iconfont icon-edit"></i>
					</p>
				</div>
				<div class="clearfix">
					<p class="fl w25">姓名</p>
					<p class="fl w70">
						{{memberData.name}}
					</p>
				</div>
				<div class="clearfix">
					<p class="fl w25">性别</p>
					<p class="fl w70">
						{{memberData.sex==1?'男':(memberData.sex==2?'女':'未知')}}
					</p>
				</div>
				<div class="clearfix">
					<p class="fl w25">手机号</p>
					<p class="fl w70">
						{{memberData.phone}}
					</p>
				</div>
				<div class="clearfix">
					<p class="fl w25">登陆密码</p>
					<p class="fl w70">
						***********
					</p>
				</div>
				<div class="clearfix addressDetail">
					<p class="fl w25">地址</p>
					<p class="fl w70 city">
						{{province}}{{city}}{{county}}{{address}}
					</p>
				</div>
				<div class="clearfix">
					<p class="fl w25">邮箱</p>
					<p class="fl w70">
						{{memberData.email}}
					</p>
				</div>
				<div class="clearfix">
					<p class="fl w25">身份证</p>
					<p class="fl w70">
						{{memberData.cardChecked==1?'待审核':(memberData.cardChecked==2?'审核':(memberData.cardChecked==3?'审核未通过':'未上传'))}}
					</p>
				</div>
			</div>
		</div>
		<ClubFoot></ClubFoot>
	</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	memberData:[],
		    	memberOption:[],
		    	img:{
		    		imgTop:'',
		    		imgBottom:''
		    	},

		    	memberParamter:[],
		    	cityList:[],
		    	countyList:[],
		    	provinceList:[],
		    	province:'',
		    	city:'',
		    	county:'',
		    	address:''
			}
		},
		components:{
		    ClubFoot
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findMemberUser_POST,
                    'type': 'POST',
                    'data':{
                       "json":json
                    },
                    'success':function (data){
                    	console.log('completeinfor',data);
                    	if(data.code==0){
                    		_this.memberData=data.data.member;

                    		_this.memberParamter=data.data.memberParamter;
                    		_this.memberOption=data.data.memberOption;
                    		_this.cityList=data.data.cityList;
                    		_this.countyList=data.data.countyList;
                    		_this.provinceList=data.data.provinceList;
                    		_this.address=_this.memberParamter.address;
                    		// 获取地址
                  			for(let i=0;i<_this.provinceList.length;i++){
                  				if(_this.provinceList[i].city_code==_this.memberParamter.provinceCode){
                  					_this.province=_this.provinceList[i].city_name;
                  					break;
                  				}
                  			}
                  			for(let i=0;i<_this.cityList.length;i++){
                  				if(_this.cityList[i].city_code==_this.memberParamter.cityCode){
                  					_this.city=_this.cityList[i].city_name;
                  					break;
                  				}
                  			}
                  			for(let i=0;i<_this.countyList.length;i++){
                  				if(_this.countyList[i].city_code==_this.memberParamter.countyCode){
                  					_this.county=_this.countyList[i].city_name;
                  					break;
                  				}
                  			}
                    	}
                    }
				})
			},
			EditProfile(){
				var _this=this;
		    	 _this.$router.push({ name: 'editprofile', params: { busId: _this.busId }})
			},
			
		},
		created:function(){
            this.loadData();

        }
	}
</script>
<style>
	
	
	
</style>


