<template>
	<div :class="[isbg?'footHeight':'footHeightImg']" v-if="isbg">
		<footer class="footImg" >
			<img :src="todo.bg1" v-if="isbg">
		</footer>
		<!-- <div v-if="isbg">{{nowBg}}</div> -->
	</div>

</template>
<script>
import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
			    todo: {
				  bg1:"static/images/2.jpg"
				},
				isbg:true
			}
		},
		methods:{
			loadData:function(){
				var _this=this;
				var busId= _this.$route.params.busId;
				var url=window.location.href;
				var json={};
				json["busId"]=busId;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findLoginImg_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	if(data.code==0){
		                	if(data.data==null){
		                		_this.isbg=false;
		                	}else{
		                		_this.isbg=true;
		                		_this.todo.bg1=data.data.loginImg;
		                	}
	                	}
	                	// console.log('Footdata',data);
	                }
				})
			}
		},
        created:function(){
            this.loadData();

        }
		
	}
</script>
<style scope>
	.footImg{
		height: 100%;
		font-size: 1rem;
	}
	.footImg img{
		width: 100%;
		height: 100%;
	}
</style>


