<template>
		<div class="tradingRecord">
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="appstoreHeight">
				<div class="mymessage clearfix"  v-for="(item,index) in shopData">
					<div class="mymessageCom w75 fl">
						<h3 >{{item.businessName}} <span class="fr">{{item.distance}}km</span></h3>
						<p><a href="javascript:;" @click="viewMap(index)"><i class="icon iconfont icon-weizhi"></i>{{item.address}}</a></p>
					</div>
					<div class="fr w20 phoneImg">
						<a :href="'tel:'+item.telephone"><i class="icon iconfont icon-iconfontdianhua"></i></a>
					</div>
				</div>
			</div>
			<ClubFoot></ClubFoot>
		</div>
</template>
<script>
	import Lib from '@/assets/js/Lib';
	import ClubFoot from "../../components/ClubFoot";
	import { Toast } from 'mint-ui';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	showTitle:"点击收起",
			    isShow: false,
			    shopData:[],
			    latitude:'',//纬度
			    longitude:''//经度
			}
		},
		components:{
		    ClubFoot
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				var mapObj,geolocation;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				mapObj = new AMap.Map('iCenter');
				mapObj.plugin('AMap.Geolocation', function () {
				    geolocation = new AMap.Geolocation({
				    	enableHighAccuracy: true,//是否使用高精度定位，默认:true
			            timeout: 10000          //超过10秒后停止定位，默认：无穷大
				    });
				    geolocation.getCurrentPosition();
				    AMap.event.addListener(geolocation, 'complete', function(data){
				    	 console.log('geolocationdata',data)
					    _this.longitude=data.position.getLng()
					    _this.latitude=data.position.getLat();
						json["longitude"]=_this.longitude;
						json["latitude"]=_this.latitude;
						console.log("json",json)
					    Lib.M.ajax({
						'url':memberAPI.activeAPI.findShop_POST,
		                'type': 'POST',
		                'data':{
		                   "json":json
		                },
		                'success':function (data){
		                	if(data.code==0){
		                		_this.shopData=data.data;
		                	}
		                	console.log('findShop',data);

		                	
		                }
					});
					});//返回定位信息
				   
				    AMap.event.addListener(geolocation, 'error', function(data){
				    	Toast({
			            	message:data.message,
			            	duration:1000
			            });
				    });
				});
			},
			Spanclick(cur){
				if(this.isShow==cur){
					this.isShow=false
					// this.title="点击展开"
				}else{
					this.isShow=cur;
					// this.title="点击收起"
				}
			},
			viewMap(pos){
				var _this=this;
				console.log(pos);
				var json={},shopLat,shopLng,myLat,myLng;
				shopLat=Number(_this.shopData[pos].latitude);
				shopLng=Number(_this.shopData[pos].longitude);
				myLat=_this.latitude;
				myLng=_this.longitude;
				// console.log(_this.shopData[pos].latitude)
				window.location="https://uri.amap.com/marker?position="+shopLng+","+ shopLat;
				// _this.$router.push({ name: 'mapview', params:{ place:json,busId:_this.busId }})
			}
		},
		mounted:function(){
            this.loadData();
        }
	}
</script>
<style scope>
	.tradingRecord .topuphead a{
		color: #fff;
	}
	.tradingRecord .mymessage{
		text-align: center;
		margin:0 0.2rem;
		border-radius: 12px;
		background: #fff;
		margin-top: 0.4rem;
	}
	.tradingRecord .mymessage .mymessageCom p{
		text-align: left;
		white-space:nowrap; 
		text-overflow:ellipsis;
		overflow:hidden;
		font-size: 0.28rem;
		padding: 0 0 0.38rem 0.32rem;
	}
	.tradingRecord .mymessage .mymessageCom .icon{
		font-size: 0.28rem;
		padding-right: 0.1rem;
	}
	.tradingRecord .mymessage .mymessageCom p a{
		color: #888888;
		font-size: 0.26rem;
		width: 100%;
	}
	.tradingRecord .mymessage .mymessageCom h3{
		font-weight: normal;
		text-align: left;
		font-size: 0.3rem;
		padding: 0.38rem 0rem 0.23rem 0.32rem;
	}
	.tradingRecord .mymessage .mymessageCom h3 span{
		/*margin-left: 1rem;*/
		font-size: 0.3rem;
		color: #999999;
	}
	.tradingRecord .mymessage .phoneImg {
		font-size: 1.2rem;
	}
	.tradingRecord .mymessage .phoneImg a{
		border-left: 1px solid #dfdfdf;
		font-size: 1.1rem;
		margin-top: 0.2rem;
	}
	.tradingRecord .mymessage .phoneImg .icon{
		font-size: 0.8rem;
		color: #29cc6d;
		margin-left: 0.15rem;

	}
</style>


