<template>
		<div class="tradingRecord qrcode">
			<div>
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a >首页</a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p>把二维码发给好友，让好友一起加入会员行列吧！</p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardNo" width="50%">
				</div>
				<p>转发此页面给好友，加入会员行列</p>
			</div>
			</div>
		</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
import { Toast } from 'mint-ui';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	urlCardNo:'',
		    	urlShare:'',
		    	shareTitle:'您的好友给您推荐一张会员卡，赶紧领取吧!',
		    	shareDesc:'识别图中二维码领取会员卡',
		    	shareImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				_this.url=_this.url.split('#');
				_this.urlShare=_this.url[0]+'#/shareQRCode/'+_this.busId;
				// _this.url=_this.url[0]+'#/home/'+_this.busId;
				// _this.url=memberAPI.activeAPI.goURL+'/html/phone/index.html#/home/'+_this.busId;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.urlShare;
				// alert(_this.urlShare)
				_this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId;
				console.log("_this.urlShare",_this.urlShare);
				Lib.M.ajax({
					'url':memberAPI.activeAPI.wxshareCard_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	// alert(JSON.stringify(data))
	                	console.log('shareQRCode',data)
	                	if(data.code==0){
	                		wx.config({
								debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
							    appId: data.data.appid, // 必填，公众号的唯一标识
							    timestamp: data.data.timestamp, // 必填，生成签名的时间戳
							    nonceStr: data.data.nonce_str, // 必填，生成签名的随机串
							    signature: data.data.signature,// 必填，签名，见附录1
							    jsApiList: ["onMenuShareAppMessage"] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
							});
							wx.ready(function(){
								//分享给朋友
								// alert(1111);
								wx.onMenuShareAppMessage({
								    title: _this.shareTitle, // 分享标题
								    desc: _this.shareDesc, // 分享描述
								    link: _this.urlShare, // 分享链接
								    imgUrl: _this.shareImg, // 分享图标
								    success: function () { 
								        // 用户确认分享后执行的回调函数
								        Toast({
							            	message:'分享成功',
							            	duration:2000
							            });
								    },
								    cancel: function () { 
								        // 用户取消分享后执行的回调函数
								        Toast({
							            	message:'取消分享',
							            	duration:1000
							            });
								    },
								    error:function(res){
								    	alert(res)
								    }
								}); 
							});
							 wx.error(function(res){
							    console.log('err', res)
							  });
	                	}
	                }
				})
			}
		},
		created:function(){
	    	var _this=this;
	    	_this.loadData();
	        
	    }
	}
</script>
<style scope>

</style>


