<template>
	<transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
		<div>
			<div class="SignPop" :class="[icon?'hasicon':'noicon']">
				<span v-show="icon"><i class="icon iconfont icon-gou"></i></span>
				<p >{{titleCom}}</p>
				<p>{{titleComTow}}</p>
			</div>
		</div>
	</transition>
</template>
<script>
	export default{
		data() {
		    return {
			    
			}
		},
		// props:['titleCom','titleComTow','icon']
		props:{
			titleCom:'',
			titleComTow:'',
			icon:{
				default:1
			}
		}
	}
</script>
<style scope>
	/*弹窗样式*/
	.SignPop{
		position: fixed;
		top: 45%;
		left: 35%;
		background: rgba(76,76,76,0.9);
		border-radius: 0.2rem;
	}
	.hasicon{
		width: 2.4rem;
		font-size: 1rem;
	}
	.noicon{
		min-width: 2rem ;
	    min-height: 1rem ;
	    padding: 0 0.2rem;
	    line-height: 1rem;
	}
/*	.SignPop span{
		margin-top: 0.3rem;
		font-size: 0.3rem;
		height: 0.3rem;
	}*/
	.SignPop span i,.SignPop span{
		font-size: 1.1rem;
		color: #fff;
		margin-top: 0.1rem;
		vertical-align: top;
		height: 1.1rem;
		line-height: 1.1rem;
	}
	.SignPop p{
		color: #fff;
		font-size:0.26rem;
	}
	.SignPop p:last-child{
		margin-bottom: 0.2rem;
	}
</style>


