<template>
		<div >
			<div class="tradingRecord" >
				<div class="clearfix topuphead">
					<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
					<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
						<a >首页</a>
					</router-link>
				</div>
				<div v-if="hasData">
					<ul
					  v-infinite-scroll="loadMore"
					  infinite-scroll-disabled="loading"
					  infinite-scroll-distance="10">
					  <li class="IntegralDataCom flowRecord clearfix"  v-for="(item,index) in recordNewData">
					  		<div class="tradingRecordCom">
								<p class="clearfix"><span class="fl">线下消费</span><span class="fr">-1000元</span></p>
								<p class="clearfix"><span class="fl">2017-10-12 14:52:12</span><span class="fr">剩余：500元</span></p>
							</div>
					  </li>
					</ul>
					<p v-show="isMsg" style="font-size: 0.3rem;color:#fff" :class="{msgType:loading}">{{msg}}</p>
					
				</div>
				<div v-else>
					<div class="nomessage">暂无交易记录</div>
				</div>
				<div class="heightline"></div>
			</div>
		</div>
</template>
<script>
	import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
			    isLoading: false,
			    hasData:false,
			    busId:0,
			    url:'',
			    page:1,
			    loading:false,
			    pageData:[],
			    isMsg:false,
			    msg:'>>>加载数据<<<',
			    recordNewData:[]
			}
		},
		methods:{
			loadData:function(){
				var _this=this;
				_this.busId= _this.$route.params.busId;
				_this.url=window.location.href;
				var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['page']=_this.page;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.findCardrecordNew_GET,
	                'type': 'GET',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('CardrecordNew',data);
	                	if(data.code==0&&data.data.page.length>0){
	                		_this.recordNewData=data.data.page;
	                		_this.hasData=true;
	                		_this.pageData=_this.recordNewData;
	                	}
	                }
				})
			},
			loadMore() {
				var _this=this;
				_this.loading = true;
				if(_this.pageData.length>=10){
					_this.isMsg=true;
				}
				if(_this.pageData!=0){
			  		_this.page+=1;
			  	}else{
			  		_this.msg='>>>加载完成<<<';
			  		setTimeout(function(){
			  			_this.isMsg=false;
			  		},1000)
            		
			  		return false;
			  	}
			  	var json={};
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['page']=_this.page;
				setTimeout(() => {
			  		Lib.M.ajax({
						'url':memberAPI.activeAPI.findCardrecordNewFenbi_GET,
		                'type': 'GET',
		                'data':{
		                   "json":json
		                },
		                'success':function (data){
		                	_this.pageData=[];
		                	if(data.data.page.length!=0){
		                		if(data.code==0&&data.data.page.length>0){
			                		_this.pageData=data.data.page;
			                		for(let i=0;i<_this.pageData.length;i++){
			                			_this.recordNewData.push(_this.pageData[i]);
			                		}
			                	}
		                	}
		                	_this.loading = false;
		                }
					})
				}, 1000);
			}
		},
		created:function(){
            this.loadData();

        }
	}
</script>
<style scope>
	.heightline{
		height: 2rem;
	}
	.tradingRecord .tradingRecordCom{
		text-align: center;
		margin:0.3rem 0.3rem 0 0.3rem;
		border-radius: 0.12rem ;
		background: #fff;
		/*height: 5rem;*/
		/*margin-top: 1rem;*/
		font-size: 1rem;
	}
	.tradingRecord .tradingRecordCom p{
		padding:  0.3rem 0.3rem 0rem 0.1rem;
		vertical-align: middle;
		font-size: 1rem;
	}
	.tradingRecord .tradingRecordCom p+p{
		padding-bottom: 0.4rem;
	}
	.tradingRecord .tradingRecordCom p span{
		font-size: 0.32rem;
		line-height: 0.32rem;
		height: 0.32rem;
	}
	.tradingRecord .tradingRecordCom p:first-child span:last-child{
		color: #2bca69;
	}
</style>


