<template>
	<div class="Topup">
		<div class="conentHeight">
			<div class="Topuptitle">
				<div class="clearfix topuphead">
					<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
					<router-link tag="a"  :to="{ name: 'home',params: { busId: busId }}" class="more fr" >
						<a >首页</a>
					</router-link>
				</div>
				<p>{{touUpTitle}}</p>
				<p class="only">{{remainingSum}}</p>
			</div>
			<div class="Topupoptions">
				<div class="options clearfix">
					<p class="Toptitle">{{congzhiTItle}}</p>
					<div v-for="(item,index) in rechargesData" class="w25 fl choose" :class='[((index+1)===isChoose)?"active":"noactive"]' @click="chooseMen(index+1)">
						<div v-if="isShiXiaoCard">
							<p>¥{{item.grRechargemoney}}</p>
							<p>
								<span>{{item.grValidDate}}{{shixiaoMoth}}{{comma}}</span>
								<span v-show="item.delayDay">{{zengsong}}{{item.delayDay}}{{zengsongUnit}}</span>
							</p>
						</div>
						<div v-else>
							<p>¥{{item.money}}</p>
							<p>
								<span v-show="isCiCard">{{item.number}}{{zengsongUnit}}{{comma}}</span>
								<span v-show="isSXCard">{{item.validDate}}{{shixiaoMoth}}{{comma}}</span>
								<span v-show="item.giveCount">{{zengsong}}{{item.giveCount}}{{zengsongUnit}}</span>
							</p>
						</div>
					</div>
				</div>
				<div class="otherM" v-if="isCuZCard">
					<p class="money">
						<label for="money">金额</label>
						<input type="number" v-model="chooseMoney" placeholder="请输入充值金额" name="" id="money">
						
					</p>
				</div>
				<div class="otherM payWay">
					<h3 class="Toptitle">支付方式</h3>
					<div>
						<p v-for="(item,index) in payTypesData" @click="choosePayType(index+1)">
							<span :class='{pitchType:(index+1)===isPayType}'><i class="icon iconfont icon-gou"></i></span>{{item.name}}
							<span v-show="isPayType==(index+1)"><i class="icon iconfont icon-gou"></i></span>
						</p>
					</div>
				</div>
				<div class="paySubmit " :class="{noPayData:noData}">
					<input class="active" type="button" name="" :value='buttonValue' @click="topUpSubmit">
					<router-link tag="a"  :to="{ name: 'tradingRecord',params: { busId: busId }}" >
						查看交易记录
					</router-link>
				</div>
			</div>
		</div>
		<ClubFoot></ClubFoot>
	</div>
</template>
<script >
	import ClubFoot from "@/components/ClubFoot";
	import Lib from '@/assets/js/Lib';
	import { Toast } from 'mint-ui';
	export default{
		data() {
		    return {
			    isLoading: false,
			    isChoose:0,
			    isPayType:1,
			    busId:0,
			    url:'',
			    chongzhiCtId:3,
			    ZhukactId:0,
			    isCuZCard:false,
			    noData:false,
			    touUpTitle:'',
			    congzhiTItle:'',
			    zengsong:'',
			    zengsongUnit:'',
			    buttonValue:'',
			    chooseMoney:'',
			    remainingSum:0,
			    isCiCard:false,
			    isSXCard:false,
			    isShiXiaoCard:false,
			    noZengSong:true,
			    comma:',',
			    shixiaoMoth:'个月',
			    istijiao:false,

			    payType:0,
			    money:0,
			    chongzhiId:[],

			    rechargesData:[],
			    payTypesData:[]
			}
		},
		components:{
		    ClubFoot
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				_this.url=window.location.href;
				_this.busId= _this.$route.params.busId;
				/** chongzhiCtId=3 储值卡充值
					chongzhiCtId=4 时效卡充值
					chongzhiCtId=5  次卡充值 
				**/
				_this.chongzhiCtId= _this.$route.query.chongzhiCtId;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json["chongzhiCtId"]=_this.chongzhiCtId;//充值类型

				Lib.M.ajax({
					'url':memberAPI.activeAPI.findRecharge_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	console.log('充值',data);
	                	if(data.code==0){
	                		// _this.noData=true;
	                		
	                		_this.payTypesData=data.data.payTypes;
	                		_this.chongzhiCtId=data.data.chongzhiCtId;
	                		_this.payType=_this.payTypesData[0].payType;
	                		_this.ZhukactId=data.data.ctId;
	                		switch(_this.chongzhiCtId){
			        			case 3://储值卡
			        				_this.isCuZCard=true;
			        				_this.touUpTitle='当前账户余额(元)';
			        				_this.congzhiTItle='充值';
			        				_this.zengsong="赠";
			        				_this.zengsongUnit="元";
			        				_this.buttonValue="确认充值";
			        				_this.remainingSum=data.data.chuZhikamoney;
			        				
			        				// 主卡
			        				if(_this.ZhukactId==_this.chongzhiCtId){
				        				if(data.data.recharges.length!=undefined){
				        					if(data.data.recharges.length==0){
					        					_this.noData=true;
					        				}
					        				_this.rechargesData=data.data.recharges;
				        				}
			        				}else{
			        					//副卡
			        					
			        				}
			        				break;
			        			case 4://实效卡
			        				_this.touUpTitle="当前可用时间(天)";
			        				_this.congzhiTItle="续期";
			        				_this.zengsong="赠";
			        				_this.zengsongUnit="天";
			        				_this.buttonValue="确认续期";
			        				var now = new Date().getTime(); 
			        				_this.remainingSum=data.data.shixiaoKaTime;
			        				_this.remainingSum=Math.ceil((_this.remainingSum-now)/(24*60*60*1000));
			        				if(_this.remainingSum<0){
			        					_this.remainingSum=0;
			        				}
			        				if(_this.remainingSum==null){
			        					_this.remainingSum=0;
			        				}
			        				// 主卡
			        				if(_this.ZhukactId==_this.chongzhiCtId){
			        					_this.isShiXiaoCard=true;
				        				if(data.data.xiaoshikaRecharges.length!=undefined){
				        					if(data.data.xiaoshikaRecharges.length==0){
					        					_this.noData=true;
					        				}
					        				_this.rechargesData=data.data.xiaoshikaRecharges;
				        				}
			        				}else{
			        					//副卡
			        					if(data.data.shixiaokarechargegive.length==0){
				        					_this.noData=true;
				        				}
				        				_this.isSXCard=true;
			        					_this.rechargesData=data.data.shixiaokarechargegive;
			        				}
			        				
			        				break;
			        			case 5://次卡
			        				_this.touUpTitle="当前剩余次数(次)";
			        				_this.congzhiTItle="续次";
			        				_this.zengsong="赠送";
			        				_this.zengsongUnit="次";
			        				_this.buttonValue="确认续次";
			        				_this.remainingSum=data.data.cikaCiShu;
			        				_this.isCiCard=true;
			        				// 主卡
			        				if(_this.ZhukactId==_this.chongzhiCtId){
			        					if(data.data.recharges.length!=undefined){
					        				// if(data.data.cikakarechargegive.length==0){
					        				// 	_this.noData=true;
					        				// }
					        				_this.rechargesData=data.data.recharges;
					        				console.log('qqq',_this.rechargesData);
				        				}
			        				}else{
			        					//副卡
			        					if(data.data.cikakarechargegive.length==0){
				        					_this.noData=true;
				        				}
			        					_this.rechargesData=data.data.cikakarechargegive;
			        				}
			        				break;
			        		}
	                	}
	                }
				})
			},
			chooseMen(index){
				var _this=this,pos;
				pos=index-1;
				if(index==_this.isChoose){
					_this.isChoose=false;
					_this.chooseMoney=0;
				}else{
					_this.isChoose= index;
					switch(_this.chongzhiCtId){
	        			case 3://储值卡
	        				_this.chooseMoney=_this.rechargesData[pos].money;
	        				break;
	        			case 4://实效卡
	        				if(_this.ZhukactId==_this.chongzhiCtId){
	        					_this.chooseMoney=_this.rechargesData[pos].grRechargemoney;
	        				}else{
	        					_this.chooseMoney=_this.rechargesData[pos].money;
	        				}
	        				break;
	        			case 5://次卡
	        				_this.chooseMoney=_this.rechargesData[pos].money;
	        				break;
	        		}
				}
			},
			choosePayType(index){
				var _this=this,pos;
				pos=index-1;
				_this.isPayType=index;
				_this.payType=_this.payTypesData[pos].payType;
			},
			topUpSubmit(){
				var _this=this,json={};
				if(_this.chooseMoney==''){
					_this.chooseMoney=0;
				}
				if(_this.chooseMoney==0){
					Toast({
		            	message:'请输入充值金额',
		            	duration:1000
		            });
		            return
				}
				if(_this.istijiao){
					return false;
				}
				json['money']=parseFloat(_this.chooseMoney);
				json['ctId']=_this.chongzhiCtId;
				json['payWay']=_this.payType;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.url;
				json['returnUrl']=_this.url;
				_this.istijiao=true;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.rechargeMemberCard_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	_this.istijiao=true;
	                	console.log('充值提交',data);
	                	if(data.code==0){
	                		window.location.href=data.data;
	                	}
	                },
	                'error':function(err){
	                	Toast({
						  	message: '充值失败',
						  	duration: 3000
						});
						_this.istijiao=true;
	                }
				})
			}
		},
		created:function(){
           this.loadData();
        }
	}

</script>
<style scope>
	.Topupoptions .options .choose p:only-child{
	  height: 1rem;
	  line-height: 1rem;
	}
</style>


